import { useState } from "react";
import { useAuth } from "../../contexts/AuthContext";
import { login as authLogin, loginAsGuest } from "../../services/auth-service";
import { Loading } from "../../components/loading";
import { SignupForm } from "../../components/signup-form/signup-form";
import { SignupResponse } from "../../domain/models/user";
import logoImg from "../../images/logo.png";
import Styles from "./login.module.scss";

export const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [showSignup, setShowSignup] = useState(false);
  const [signupSuccess, setSignupSuccess] = useState<string>("");
  const { login } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsLoading(true);

    try {
      const response = await authLogin({ username, password });

      if (response.success && response.user) {
        login(response.user);
      } else {
        setError(response.message || "Erro ao fazer login");
      }
    } catch (err) {
      setError("Erro inesperado. Tente novamente.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleGuestLogin = async () => {
    setError("");
    setIsLoading(true);

    try {
      const response = await loginAsGuest();

      if (response.success && response.user) {
        login(response.user);
      } else {
        setError("Erro ao entrar como convidado");
      }
    } catch (err) {
      setError("Erro inesperado. Tente novamente.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleShowSignup = () => {
    setShowSignup(true);
    setError("");
    setSignupSuccess("");
  };

  const handleBackToLogin = () => {
    setShowSignup(false);
    setError("");
    setSignupSuccess("");
  };

  const handleSignupSuccess = (response: SignupResponse) => {
    setSignupSuccess(response.message || "Cadastro realizado com sucesso!");
    setShowSignup(false);
  };

  if (isLoading) {
    return (
      <div className={Styles.LoginContainer}>
        <Loading text="Entrando..." />
      </div>
    );
  }

  if (showSignup) {
    return (
      <SignupForm
        onSignupSuccess={handleSignupSuccess}
        onBackToLogin={handleBackToLogin}
      />
    );
  }

  return (
    <div className={Styles.LoginContainer}>
      <form className={Styles.LoginForm} onSubmit={handleSubmit}>
        <div className={Styles.LoginLogo}>
          <img
            src={logoImg}
            alt="Acesso Total Chess"
            className={Styles.LogoImg}
          />
        </div>

        <div className={Styles.WelcomeMessage}>Bem-vindo ao Acesso Total</div>

        {error && <div className={Styles.ErrorMessage}>{error}</div>}
        {signupSuccess && (
          <div className={Styles.SuccessMessage}>{signupSuccess}</div>
        )}

        <div className={Styles.FormGroup}>
          <label htmlFor="username" className={Styles.FormLabel}>
            Usuário
          </label>
          <input
            type="text"
            id="username"
            className={Styles.FormInput}
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="Digite seu email ou usuário"
            required
            disabled={isLoading}
          />
        </div>

        <div className={Styles.FormGroup}>
          <label htmlFor="password" className={Styles.FormLabel}>
            Senha
          </label>
          <input
            type="password"
            id="password"
            className={Styles.FormInput}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Digite sua senha"
            required
            disabled={isLoading}
          />
          <button
            type="button"
            className={Styles.ForgotPasswordLink}
            onClick={() => {
              // TODO: Implementar funcionalidade de esqueci minha senha
            }}
          >
            Esqueci minha senha
          </button>
        </div>

        <button
          type="submit"
          className={Styles.LoginButton}
          disabled={isLoading}
        >
          Entrar
        </button>

        {/* <div className={Styles.SignupLinkContainer}> */}
        <button
          type="button"
          className={Styles.SignupLink}
          onClick={handleShowSignup}
          disabled={isLoading}
        >
          Criar nova conta
        </button>
        {/* </div> */}

        <div className={Styles.GuestLinkContainer}>
          <button
            type="button"
            className={Styles.GuestLink}
            onClick={handleGuestLogin}
            disabled={isLoading}
          >
            Entrar como convidado
          </button>
        </div>
      </form>
    </div>
  );
};
