import React from "react";
import {
  HashRouter as Router,
  Routes,
  Route,
  useLocation,
} from "react-router-dom";
import { Home } from "./pages/home";
import { Puzzle } from "./pages/puzzle";
import { Game } from "./pages/game/game";
import { Games } from "./pages/games/games";
import { Statistics } from "./pages/statistics/statistics";
import { Profile } from "./pages/profile";
import { Login } from "./pages/login";
import { CreatePuzzle } from "./pages/create-puzzle";
import { AdminHome } from "./pages/admin-home";
import { AccountDeletion } from "./pages/account-deletion";
import { FreeAnalysis } from "./pages/free-analysis";
import { Opening } from "./pages/opening/opening";
import { VerifyEmail } from "./pages/verify-email/verify-email";
import { Support } from "./pages/support/support";
import { AuthProvider, useAuth } from "./contexts/AuthContext";
import { PuzzleProvider } from "./contexts/PuzzleContext";
import { LoadingContainer } from "./components/loading/loading-container";

const AppContent: React.FC = () => {
  const { isAuthenticated, isLoading, user } = useAuth();
  const location = useLocation();

  if (isLoading) {
    return <LoadingContainer />;
  }

  // Rotas que devem ser acessíveis sem autenticação
  if (location.pathname === "/verify-email") {
    return <VerifyEmail />;
  }
  
  if (location.pathname === "/suporte") {
    return <Support />;
  }

  if (!isAuthenticated) {
    return <Login />;
  }

  return (
    <PuzzleProvider>
      <Routes>
        <Route
          path="/"
          element={user?.role === "admin" ? <CreatePuzzle /> : <Home />}
        />
        <Route path="/puzzle" element={<Puzzle />} />
        <Route path="/partida/:id" element={<Game />} />
        <Route path="/partidas" element={<Games />} />
        <Route
          path="/estatisticas"
          element={user?.role === "admin" ? <AdminHome /> : <Statistics />}
        />
        <Route path="/perfil" element={<Profile />} />
        <Route path="/exclusao-conta" element={<AccountDeletion />} />
        <Route path="/aberturas" element={<Opening />} />
        <Route path="/analise-livre" element={<FreeAnalysis />} />
      </Routes>
    </PuzzleProvider>
  );
};

export const App: React.FC = () => {
  return (
    <Router>
      <AuthProvider>
        <AppContent />
      </AuthProvider>
    </Router>
  );
};

export default App;
