import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import classNames from "classnames";
import logoImg from "../../images/logo.png";
import { isRunningInApp } from "../../utils/platform";
import { useUser } from "../../hooks/useUser";
import { ReactComponent as HomeIcon } from "../../icons/home.svg";
import { ReactComponent as PuzzleIcon } from "../../icons/puzzle.svg";
import { ReactComponent as ChessboardIcon } from "../../icons/pawn.svg";
import { ReactComponent as ChartIcon } from "../../icons/chart.svg";
import { ReactComponent as MenuIcon } from "../../icons/menu.svg";
import Styles from "./app-template.module.scss";

interface AppTemplateProps {
  children: React.ReactNode;
  title?: string;
  withChessboard?: boolean;
  withoutHeaderShadow?: boolean;
}

export const AppTemplate: React.FC<AppTemplateProps> = ({
  children,
  title,
  withChessboard,
  withoutHeaderShadow = false,
}) => {
  const location = useLocation();
  const navigate = useNavigate();
  const isInApp = isRunningInApp();
  const user = useUser();

  const handleNavigate = (path: string) => {
    navigate(path);
  };

  const getUserInitial = () => {
    const firstName = user.getFirstName();
    return firstName ? firstName.charAt(0).toUpperCase() : "U";
  };

  return (
    <div className={Styles.AppTemplate}>
      <header
        className={classNames(Styles.AppHeader, {
          [Styles.AppHeaderInApp]: isInApp,
          [Styles.WithChessboard]: withoutHeaderShadow,
        })}
      >
        <div className={Styles.HeaderContent}>
          <div className={Styles.LogoContainer}>
            <img src={logoImg} alt="Logo" className={Styles.LogoImg} />
          </div>
          {title && (
            <div className={Styles.PageTitle}>
              <h1>{title}</h1>
            </div>
          )}
          <div className={Styles.ProfileContainer}>
            <div
              className={Styles.ProfileImage}
              onClick={() => handleNavigate("/perfil")}
            >
              {user.user?.profileImage ? (
                <img
                  src={user.user.profileImage}
                  alt="Foto de perfil"
                  className={Styles.ProfileImagePhoto}
                />
              ) : (
                getUserInitial()
              )}
            </div>
          </div>
        </div>
      </header>

      <main
        className={classNames(
          withChessboard ? Styles.AppMainWithChessboard : Styles.AppMain,
          { [Styles.InApp]: isInApp }
        )}
      >
        {children}
      </main>

      <footer
        className={classNames(Styles.AppFooter, {
          [Styles.AppFooterInApp]: isInApp,
        })}
      >
        <div className={Styles.FooterNav}>
          <button
            className={classNames(Styles.NavItem, {
              [Styles.NavItemActive]: location.pathname === "/",
            })}
            onClick={() => handleNavigate("/")}
          >
            <HomeIcon className={Styles.NavIcon} />
            <span className={Styles.NavLabel}>Início</span>
          </button>
          <button
            className={classNames(Styles.NavItem, {
              [Styles.NavItemActive]: location.pathname === "/estatisticas",
            })}
            onClick={() => handleNavigate("/estatisticas")}
          >
            <ChartIcon className={Styles.NavIcon} />
            <span className={Styles.NavLabel}>Progresso</span>
          </button>
          <button
            className={classNames(Styles.NavItem, {
              [Styles.NavItemActive]: location.pathname === "/puzzle",
            })}
            onClick={() => handleNavigate("/puzzle")}
          >
            <PuzzleIcon className={Styles.NavIcon} />
            <span className={Styles.NavLabel}>Exercícios</span>
          </button>
          <button
            className={classNames(Styles.NavItem, {
              [Styles.NavItemActive]:
                location.pathname.includes("partida") ||
                location.pathname.includes("analise-livre"),
            })}
            onClick={() => handleNavigate("/partidas")}
          >
            <ChessboardIcon className={Styles.NavIcon} />
            <span className={Styles.NavLabel}>Partidas</span>
          </button>
          <button
            className={classNames(Styles.NavItem, {
              [Styles.NavItemActive]: location.pathname === "/perfil",
            })}
            onClick={() => handleNavigate("/perfil")}
          >
            <MenuIcon className={Styles.NavIcon} />
            <span className={Styles.NavLabel}>Mais</span>
          </button>
        </div>
      </footer>
    </div>
  );
};
