import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { Puzzle } from '../domain/models/puzzle';
import { fetchPuzzles } from '../services/puzzle-service';

interface PuzzleContextType {
  puzzles: Puzzle[];
  isLoading: boolean;
  error: string | null;
  refetch: () => Promise<void>;
}

const PuzzleContext = createContext<PuzzleContextType | undefined>(undefined);

interface PuzzleProviderProps {
  children: ReactNode;
}

export const PuzzleProvider: React.FC<PuzzleProviderProps> = ({ children }) => {
  const [puzzles, setPuzzles] = useState<Puzzle[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadPuzzles = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const puzzleData = await fetchPuzzles();
      setPuzzles(puzzleData);
    } catch (err) {
      console.error("Failed to load puzzles:", err);
      setError("Erro ao carregar exercícios. Tente novamente.");
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadPuzzles();
  }, []);

  const refetch = async () => {
    await loadPuzzles();
  };

  const value: PuzzleContextType = {
    puzzles,
    isLoading,
    error,
    refetch,
  };

  return (
    <PuzzleContext.Provider value={value}>
      {children}
    </PuzzleContext.Provider>
  );
};

export const usePuzzles = (): PuzzleContextType => {
  const context = useContext(PuzzleContext);
  if (context === undefined) {
    throw new Error('usePuzzles must be used within a PuzzleProvider');
  }
  return context;
};