import { useState } from "react";
import { Puzzle } from "../../domain/models/puzzle";
import { updatePuzzle } from "../../services/puzzle-service";
import Styles from "./exercise-end-modal.module.scss";
import classNames from "classnames";

interface ExerciseEndModalProps {
  puzzle: Puzzle;
  onClose: () => void;
  onSuccess: () => void;
}

export function ExerciseEndModal({ 
  puzzle, 
  onClose, 
  onSuccess 
}: ExerciseEndModalProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  // Filter moves based on who moves first in the puzzle
  // If white moves first: even indices (0, 2, 4...) are white moves
  // If black moves first: even indices (0, 2, 4...) are black moves
  const isWhiteToMove = puzzle.firstMove === "white";
  const userMoveIndices = puzzle.expectedMoves
    .map((_, index) => index)
    .filter((index) => index % 2 === 0); // User always plays on even indices (0, 2, 4...)

  const [selectedMoveIndex, setSelectedMoveIndex] = useState<number>(
    userMoveIndices[userMoveIndices.length - 1] || 0
  );

  const newExpectedMoves = puzzle.expectedMoves.slice(0, selectedMoveIndex + 1);

  const handleConfirm = async () => {
    setIsLoading(true);
    setError(null);

    try {
      const updateData = {
        expectedMoves: newExpectedMoves
      };

      await updatePuzzle(puzzle.id, updateData);
      onSuccess();
      onClose();
    } catch (err) {
      setError("Erro ao atualizar o exercício. Tente novamente.");
      console.error("Error updating puzzle:", err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    onClose();
  };

  return (
    <div className={Styles.ModalOverlay}>
      <div className={Styles.ModalContainer}>
        <div className={Styles.ModalHeader}>
          <h3>Definir Final do Exercício</h3>
        </div>

        <div className={Styles.ModalContent}>
          <div className={Styles.MovesSelection}>
            <h4>Selecione o lance final do exercício ({isWhiteToMove ? 'Brancas' : 'Pretas'}):</h4>
            <div className={Styles.MovesGrid}>
              {userMoveIndices.map((originalIndex) => {
                const move = puzzle.expectedMoves[originalIndex];
                return (
                  <button
                    key={originalIndex}
                    className={classNames(Styles.MoveButton, {
                      [Styles.MoveButtonSelected]: originalIndex === selectedMoveIndex
                    })}
                    onClick={() => setSelectedMoveIndex(originalIndex)}
                  >
                    <span className={Styles.MoveNumber}>
                      {Math.floor(originalIndex / 2) + 1}
                      {originalIndex % 2 === 0 ? '.' : '...'}
                    </span>
                    <span className={Styles.MoveNotation}>{move.move}</span>
                  </button>
                );
              })}
            </div>
          </div>

          {error && (
            <div className={Styles.ErrorMessage}>
              {error}
            </div>
          )}
        </div>

        <div className={Styles.ModalActions}>
          <button 
            className={classNames(Styles.Button, Styles.CancelButton)}
            onClick={handleCancel}
            disabled={isLoading}
          >
            Cancelar
          </button>
          <button 
            className={classNames(Styles.Button, Styles.ConfirmButton)}
            onClick={handleConfirm}
            disabled={isLoading}
          >
            {isLoading ? "Salvando..." : "Confirmar"}
          </button>
        </div>
      </div>
    </div>
  );
}