interface StockfishEvaluation {
  score: number;
  mate?: number;
  bestMove?: string;
  depth: number;
}

interface LichessCloudEvalResponse {
  fen: string;
  knodes: number;
  depth: number;
  pvs: Array<{
    moves: string;
    cp?: number;
    mate?: number;
  }>;
}

class StockfishService {
  private readonly lichessCloudEvalUrl = 'https://lichess.org/api/cloud-eval';

  async evaluatePosition(fen: string, depth: number = 15): Promise<StockfishEvaluation> {
    try {
      const response = await fetch(`${this.lichessCloudEvalUrl}?fen=${encodeURIComponent(fen)}&multiPv=1`, {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error(`Lichess API error: ${response.status}`);
      }

      const data: LichessCloudEvalResponse = await response.json();

      if (!data.pvs || data.pvs.length === 0) {
        throw new Error('No evaluation data received');
      }

      const bestLine = data.pvs[0];
      const bestMove = bestLine.moves.split(' ')[0];

      return {
        score: bestLine.cp ? bestLine.cp / 100 : 0, // Convert centipawns to pawns
        mate: bestLine.mate,
        bestMove: bestMove,
        depth: data.depth,
      };
    } catch (error) {
      console.error('Error evaluating position with Stockfish:', error);
      throw new Error('Failed to evaluate position');
    }
  }

  async getBestMove(fen: string, depth: number = 15): Promise<string | null> {
    try {
      const evaluation = await this.evaluatePosition(fen, depth);
      return evaluation.bestMove || null;
    } catch (error) {
      console.error('Error getting best move:', error);
      return null;
    }
  }

  formatEvaluation(evaluation: StockfishEvaluation): string {
    if (evaluation.mate !== undefined) {
      const mateValue = evaluation.mate;
      if (mateValue > 0) {
        return `#${mateValue}`;
      } else if (mateValue < 0) {
        return `#${Math.abs(mateValue)}`;
      }
    }

    const score = evaluation.score;
    if (Math.abs(score) < 0.01) {
      return '0.00';
    }

    const formattedScore = Math.abs(score).toFixed(2);
    return score > 0 ? `+${formattedScore}` : `-${formattedScore}`;
  }

  getEvaluationColor(evaluation: StockfishEvaluation): 'white' | 'black' | 'equal' {
    if (evaluation.mate !== undefined) {
      return evaluation.mate > 0 ? 'white' : 'black';
    }

    const score = evaluation.score;
    if (Math.abs(score) < 0.15) {
      return 'equal';
    }

    return score > 0 ? 'white' : 'black';
  }
}

export const stockfishService = new StockfishService();
export type { StockfishEvaluation };