import { useState } from "react";
import { createPuzzlesFromPGN } from "../../services/puzzle-service";
import { AppTemplate } from "../../components/app-template";
import Styles from "./create-puzzle.module.scss";

export const CreatePuzzle = () => {
  const [theme, setTheme] = useState("");
  const [pgn, setPgn] = useState("");
  const [message, setMessage] = useState<{
    type: "success" | "error";
    text: string;
  } | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setMessage(null);

    try {
      const result = await createPuzzlesFromPGN({ theme, pgn });

      if (result.success) {
        setMessage({
          type: "success",
          text: result.message || "Puzzle criado com sucesso!",
        });
        // Reset form
        setTheme("");
        setPgn("");
      } else {
        setMessage({
          type: "error",
          text: result.message || "Erro ao criar puzzle. Tente novamente.",
        });
      }
    } catch (error) {
      setMessage({
        type: "error",
        text:
          error instanceof Error
            ? error.message
            : "Erro ao criar puzzle. Tente novamente.",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const resetForm = () => {
    setTheme("");
    setPgn("");
    setMessage(null);
  };

  return (
    <AppTemplate>
      <div className={Styles.CreatePuzzleContainer}>
      <div className={Styles.CreatePuzzleHeader}>
        <h1 className={Styles.CreatePuzzleTitle}>Adicionar exercícios</h1>
        {/* <p className={Styles.CreatePuzzleSubtitle}>
          Preencha o tema e cole o PGN para criar novos exercícios
        </p> */}
      </div>

      {message && (
        <div className={message.type === 'success' ? Styles.SuccessMessage : Styles.ErrorMessage}>{message.text}</div>
      )}

      <form className={Styles.PuzzleForm} onSubmit={handleSubmit}>
        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="theme">
            Tema
          </label>
          <input
            type="text"
            id="theme"
            className={Styles.FormInput}
            value={theme}
            onChange={(e) => setTheme(e.target.value)}
            placeholder="Ex: Finais, Táticas, Combinações..."
            required
          />
        </div>

        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="pgn">
            PGN
          </label>
          <textarea
            id="pgn"
            className={Styles.FormTextarea}
            value={pgn}
            onChange={(e) => setPgn(e.target.value)}
            placeholder="Cole aqui o PGN completo com todos exercícios deste tema..."
            required
          />
        </div>

        <div className={Styles.FormActions}>
          <button type="button" className={Styles.BtnSecondary} onClick={resetForm}>
            Limpar
          </button>
          <button type="submit" className={Styles.BtnPrimary} disabled={isLoading}>
            {isLoading ? "Adicionando..." : "Adicionar"}
          </button>
        </div>
      </form>
      </div>
    </AppTemplate>
  );
};
