import React, { useState, useEffect } from "react";
import classNames from "classnames";
import { getAllUsers, AdminUser } from "../../services/user-service";
import { LoadingContainer } from "../loading/loading-container";
import Styles from "./admin-user-list.module.scss";

interface AdminUserListProps {
  onUserSelect: (user: AdminUser) => void;
}

export const AdminUserList: React.FC<AdminUserListProps> = ({
  onUserSelect,
}) => {
  const [users, setUsers] = useState<AdminUser[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        setLoading(true);
        setError(null);

        const response = await getAllUsers();

        if (response.success) {
          setUsers(response.users);
        } else {
          setError(response.message || "Erro ao carregar usuários");
        }
      } catch (error) {
        console.error("Erro ao buscar usuários:", error);
        setError("Erro ao carregar usuários");
      } finally {
        setLoading(false);
      }
    };

    fetchUsers();
  }, []);

  if (loading) {
    return <LoadingContainer />;
  }

  if (error) {
    return (
      <div className={Styles.AdminUserListContainer}>
        <div className={Styles.ErrorMessage}>{error}</div>
      </div>
    );
  }

  return (
    <div className={Styles.AdminUserListContainer}>
      <div className={Styles.Header}>
        <h2 className={Styles.Title}>Gerenciar Usuários</h2>
        <p className={Styles.Subtitle}>
          Clique em um usuário para ver seus detalhes e gerenciar sua conta
        </p>
      </div>

      <div className={Styles.UserTable}>
        <div className={Styles.TableHeader}>
          <div className={Styles.HeaderCell}>Email</div>
          <div className={Styles.HeaderCell}>Status</div>
        </div>

        <div className={Styles.TableBody}>
          {users.map((user) => (
            <div
              key={user.id}
              className={Styles.UserRow}
              onClick={() => onUserSelect(user)}
            >
              <div className={Styles.UserEmail}>{user.email}</div>
              <div className={Styles.UserStatus}>
                <span
                  className={classNames(Styles.StatusBadge, {
                    [Styles.StatusActive]: user.status === "ACTIVE",
                    [Styles.StatusBlocked]: user.status === "INACTIVE",
                  })}
                >
                  {user.status === "ACTIVE" ? "Ativo" : "Inativo"}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>

      {users.length === 0 && (
        <div className={Styles.EmptyState}>
          <p>Nenhum usuário encontrado</p>
        </div>
      )}
    </div>
  );
};
