import React, { useState } from "react";
import classNames from "classnames";
import { useAuth } from "../../contexts/AuthContext";
import { useUser } from "../../hooks/useUser";
import { ReactComponent as HomeIcon } from "../../icons/home.svg";
import { ReactComponent as PuzzleIcon } from "../../icons/puzzle.svg";
import { ReactComponent as ChessboardIcon } from "../../icons/pawn.svg";
import { ReactComponent as ChartIcon } from "../../icons/chart.svg";
import { ReactComponent as PlusIcon } from "../../icons/plus.svg";
import { ReactComponent as BookIcon } from "../../icons/book.svg";
import { useNavigate } from "react-router-dom";
import { AppTemplate } from "../../components/app-template";
import { ProfileImageUpload } from "../../components/profile-image-upload/profile-image-upload";
import Styles from "./profile.module.scss";

export const Profile: React.FC = () => {
  const { user, logout, isLoading } = useAuth();
  const userHook = useUser();
  const navigate = useNavigate();
  const [uploadMessage, setUploadMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  const handleLogout = () => {
    logout();
    navigate("/");
  };

  const handleNavigate = (path: string) => {
    navigate(path);
  };

  const handleUploadSuccess = () => {
    setUploadMessage({ type: 'success', text: 'Foto de perfil atualizada com sucesso!' });
    setTimeout(() => setUploadMessage(null), 5000);
  };

  const handleUploadError = (error: string) => {
    setUploadMessage({ type: 'error', text: error });
    setTimeout(() => setUploadMessage(null), 5000);
  };

  if (isLoading) {
    return (
      <AppTemplate>
        <div className={Styles.UserInfoSection}>
          <div>Carregando...</div>
        </div>
      </AppTemplate>
    );
  }

  return (
    <AppTemplate>
      <div className={Styles.UserInfoSection}>
        <div className={Styles.UserAvatar}>
          <ProfileImageUpload
            className={Styles.UserAvatarCompact}
            onUploadSuccess={handleUploadSuccess}
            onUploadError={handleUploadError}
          />
        </div>

        <div className={Styles.UserDetailsSection}>
          <div className={Styles.UserBasicInfo}>
            <h3 className={Styles.UserName}>{userHook.getFirstName()}</h3>
            <p className={Styles.UserEmail}>{user?.email}</p>
          </div>
        </div>

        <div className={Styles.UserBadges}>
          {user?.courseAccess && (
            <span className={classNames(Styles.RoleBadge, Styles.RoleActive)}>
              Curso Ativo
            </span>
          )}
          {user?.role && (
            <span
              className={classNames(
                Styles.RoleBadge,
                Styles[
                  `Role${user?.role
                    ?.charAt(0)
                    .toUpperCase()}${user?.role?.slice(1)}`
                ]
              )}
            >
              {user?.role === "admin"
                ? "Admin"
                : user?.role === "user"
                ? "Usuário"
                : "Convidado"}
            </span>
          )}
        </div>
      </div>

      {uploadMessage && (
        <div className={classNames(Styles.UploadMessage, {
          [Styles.UploadSuccess]: uploadMessage.type === 'success',
          [Styles.UploadError]: uploadMessage.type === 'error'
        })}>
          {uploadMessage.text}
        </div>
      )}

      <div className={Styles.ProfileNavigation}>
        <h3 className={Styles.SectionTitle}>Ferramentas Exclusivas</h3>
        <div
          className={classNames(Styles.NavItemProfile, Styles.NavItemHighlight)}
          onClick={() => handleNavigate("/aberturas")}
        >
          <BookIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Aberturas</span>
        </div>
        <div
          className={classNames(Styles.NavItemProfile, Styles.NavItemHighlight)}
          onClick={() => handleNavigate("/analise-livre")}
        >
          <PlusIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Nova análise</span>
        </div>
        
        <h3 className={Styles.SectionTitle}>Navegação Rápida</h3>
        <div
          className={Styles.NavItemProfile}
          onClick={() => handleNavigate("/")}
        >
          <HomeIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Home</span>
        </div>
        <div
          className={Styles.NavItemProfile}
          onClick={() => handleNavigate("/estatisticas")}
        >
          <ChartIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Estatísticas</span>
        </div>
        <div
          className={Styles.NavItemProfile}
          onClick={() => handleNavigate("/puzzle")}
        >
          <PuzzleIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Exercícios</span>
        </div>
        <div
          className={Styles.NavItemProfile}
          onClick={() => handleNavigate("/partidas")}
        >
          <ChessboardIcon className={Styles.NavIconProfile} />
          <span className={Styles.NavLabelProfile}>Partidas</span>
        </div>
      </div>

      <div className={Styles.AccountActions}>
        <button className={Styles.LogoutBtn} onClick={handleLogout}>
          Sair da Conta
        </button>

        {user?.role !== "guest" && (
          <button
            className={Styles.DeleteAccountBtn}
            onClick={() => handleNavigate("/exclusao-conta")}
          >
            Solicitar Exclusão de Conta
          </button>
        )}
      </div>
    </AppTemplate>
  );
};
