import React, { useState } from "react";
import classNames from "classnames";
import { AdminUser, updateUserStatus } from "../../services/user-service";
import { ReactComponent as ChevronLeft } from "../../icons/chevron-left.svg";
import Styles from "./admin-user-details.module.scss";

interface AdminUserDetailsProps {
  user: AdminUser;
  onBack: () => void;
  onUserUpdated: (updatedUser: AdminUser) => void;
}

export const AdminUserDetails: React.FC<AdminUserDetailsProps> = ({
  user,
  onBack,
  onUserUpdated,
}) => {
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState<{
    type: "success" | "error";
    text: string;
  } | null>(null);

  const handleStatusChange = async (newStatus: "ACTIVE" | "INACTIVE") => {
    if (newStatus === user.status) return;

    try {
      setLoading(true);
      setMessage(null);

      const response = await updateUserStatus(user.id, newStatus);

      if (response.success) {
        const updatedUser = { ...user, status: newStatus };
        onUserUpdated(updatedUser);

        setMessage({
          type: "success",
          text:
            response.message ||
            `Usuário ${
              newStatus === "ACTIVE" ? "ativado" : "desativado"
            } com sucesso`,
        });
      } else {
        setMessage({
          type: "error",
          text: response.message || "Erro ao alterar status do usuário",
        });
      }
    } catch (error) {
      console.error("Erro ao alterar status:", error);
      setMessage({
        type: "error",
        text: "Erro ao alterar status do usuário",
      });
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString: string) => {
    try {
      return new Date(dateString).toLocaleDateString("pt-BR", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
        hour: "2-digit",
        minute: "2-digit",
      });
    } catch {
      return "Data inválida";
    }
  };

  return (
    <div className={Styles.AdminUserDetailsContainer}>
      <div className={Styles.Header}>
        <button
          className={Styles.BackButton}
          onClick={onBack}
          disabled={loading}
        >
          <ChevronLeft className={Styles.BackIcon} />
          <span>Voltar</span>
        </button>
        <h2 className={Styles.Title}>Detalhes do Usuário</h2>
      </div>

      {message && (
        <div
          className={classNames(Styles.Message, {
            [Styles.MessageSuccess]: message.type === "success",
            [Styles.MessageError]: message.type === "error",
          })}
        >
          {message.text}
        </div>
      )}

      <div className={Styles.UserCard}>
        <div className={Styles.UserProfile}>
          <div className={Styles.ProfileImageContainer}>
            {user.profileImage ? (
              <img
                src={user.profileImage}
                alt={`Foto de ${user.name}`}
                className={Styles.ProfileImage}
              />
            ) : (
              <div className={Styles.ProfileImagePlaceholder}>
                <span>{user.name?.charAt(0)?.toUpperCase() || "U"}</span>
              </div>
            )}
          </div>

          <div className={Styles.UserInfo}>
            <div className={Styles.UserName}>{user.name}</div>
            <div className={Styles.UserEmail}>{user.email}</div>
            <div className={Styles.UserStatus}>
              <span
                className={classNames(Styles.StatusBadge, {
                  [Styles.StatusActive]: user.status === "ACTIVE",
                  [Styles.StatusBlocked]: user.status === "INACTIVE",
                })}
              >
                {user.status === "ACTIVE" ? "Ativo" : "Inativo"}
              </span>
            </div>
          </div>
        </div>

        <div className={Styles.UserDetails}>
          <div className={Styles.DetailItem}>
            <span className={Styles.DetailLabel}>E-mail:</span>
            <span className={Styles.DetailValue}>{user.email}</span>
          </div>

          <div className={Styles.DetailItem}>
            <span className={Styles.DetailLabel}>Nome:</span>
            <span className={Styles.DetailValue}>{user.name}</span>
          </div>

          <div className={Styles.DetailItem}>
            <span className={Styles.DetailLabel}>Status:</span>
            <span className={Styles.DetailValue}>
              <span
                className={classNames(Styles.StatusBadge, {
                  [Styles.StatusActive]: user.status === "ACTIVE",
                  [Styles.StatusBlocked]: user.status === "INACTIVE",
                })}
              >
                {user.status === "ACTIVE" ? "Ativo" : "Inativo"}
              </span>
            </span>
          </div>

          {user.usernameChessCom && (
            <div className={Styles.DetailItem}>
              <span className={Styles.DetailLabel}>Username Chess.com:</span>
              <span className={Styles.DetailValue}>
                {user.usernameChessCom}
              </span>
            </div>
          )}

          {user.usernameLichess && (
            <div className={Styles.DetailItem}>
              <span className={Styles.DetailLabel}>Username Lichess:</span>
              <span className={Styles.DetailValue}>{user.usernameLichess}</span>
            </div>
          )}

          <div className={Styles.DetailItem}>
            <span className={Styles.DetailLabel}>Criado em:</span>
            <span className={Styles.DetailValue}>
              {formatDate(user.createdAt)}
            </span>
          </div>

          <div className={Styles.DetailItem}>
            <span className={Styles.DetailLabel}>Última atualização:</span>
            <span className={Styles.DetailValue}>
              {formatDate(user.updatedAt)}
            </span>
          </div>
        </div>

        <div className={Styles.Actions}>
          {user.status === "ACTIVE" ? (
            <button
              className={classNames(Styles.ActionButton, Styles.BlockButton)}
              onClick={() => handleStatusChange("INACTIVE")}
              disabled={loading}
            >
              {loading ? "Desativando..." : "Desativar Usuário"}
            </button>
          ) : (
            <button
              className={classNames(Styles.ActionButton, Styles.ActivateButton)}
              onClick={() => handleStatusChange("ACTIVE")}
              disabled={loading}
            >
              {loading ? "Ativando..." : "Ativar Usuário"}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};
