  // Mock data para exemplo
  const exampleStats = {
    totalExercisesCompleted: 247,
    totalCorrectMoves: 1834,
    averageSuccessRate: 78.5,
    currentStreak: 12,
    longestStreak: 28,
    totalTimeSpent: 18240, // in seconds (5h 4min)
    rank: "Iniciante Avançado",
    experiencePoints: 2840,
  };

  const currentStats = showExample ? exampleStats : userStats;
  const hasEnoughData = userStats.totalExercisesCompleted >= 10; // Threshold para mostrar estatísticas

  const themeStats: ThemeStats[] = showExample
