import { useState, useEffect, useCallback, useMemo } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { Chess, Square } from "chess.js";
import {
  Puzzle as PuzzleInterface,
  PuzzleMove,
} from "../../domain/models/puzzle";
import { normalizarMovimento } from "../../utils";
import { usePuzzles } from "../../contexts/PuzzleContext";
import { useUser } from "../../hooks/useUser";
import { ChessboardControls } from "../../components/chessboard-controls/chessboard-controls";
import { Chessboard } from "../../components/chessboard";
import { AdminEdit } from "../../components/admin-edit/admin-edit";
import { AdminButtons } from "../../components/admin-buttons/admin-buttons";
import { ExerciseEndModal } from "../../components/exercise-end-modal/exercise-end-modal";
import { DeletePuzzleModal } from "../../components/delete-puzzle-modal/delete-puzzle-modal";
import { Loading } from "../../components/loading";
import { StockfishEvaluation } from "../../components/stockfish-evaluation/stockfish-evaluation";
import { PuzzleFilterModal } from "../../components/puzzle-filter-modal";
import { AppTemplate } from "../../components/app-template";
import "./puzzle.css";
import {
  CustomizedSquareVariant,
  ICustomizedSquare,
} from "../../domain/models/customized-square";
import { Nullable } from "../../domain/models";
import { IResult, setInitialResult } from "../../domain/models/result";

export const Puzzle = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [game, setGame] = useState(new Chess());
  const [currentPuzzle, setCurrentPuzzle] = useState(0);
  const [expectedMoves, setExpectedMoves] = useState<ReadonlyArray<PuzzleMove>>(
    []
  );
  const [possibleMoves, setPossibleMoves] = useState<ReadonlyArray<Square>>([]);
  const [currentMove, setCurrentMove] = useState(0);

  const [firstMoveIsBlack, setFirstMoveIsBlack] = useState(false);
  const [result, setResult] = useState<IResult>(setInitialResult());
  const [selectedPiece, setSelectedPiece] = useState<Nullable<Square>>(null);

  const [customizedSquare, setCustomizedSquare] =
    useState<Nullable<ICustomizedSquare>>(null);

  // Get puzzles from context
  const { puzzles: allPuzzles, isLoading, error, refetch } = usePuzzles();
  const { isAdmin } = useUser();
  const [showEvaluationBar, setShowEvaluationBar] = useState(false);
  const [showExplanation, setShowExplanation] = useState(false);
  const [showHint, setShowHint] = useState(false);
  const [showHintEdit, setShowHintEdit] = useState(false);
  const [showExplanationEdit, setShowExplanationEdit] = useState(false);
  const [showExerciseEndModal, setShowExerciseEndModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [showFilterModal, setShowFilterModal] = useState(false);
  const [refreshKey, setRefreshKey] = useState(0);
  const [navigatingFromGame, setNavigatingFromGame] = useState(() => {
    // Verificar se há um exercício específico sendo navegado
    const exerciseId = new URLSearchParams(window.location.search).get(
      "exerciseId"
    );
    return (
      !!exerciseId || sessionStorage.getItem("navigatingFromGame") === "true"
    );
  });

  // Handle error from context
  useEffect(() => {
    if (error) {
      setResult({
        status: "error",
        message: error,
      });
    }
  }, [error]);

  // Cleanup sessionStorage quando o componente é desmontado (navegação para outra página)
  useEffect(() => {
    return () => {
      // Só limpar se não estivermos navegando para uma partida
      const currentPath = window.location.pathname;
      if (!currentPath.includes("/partida/")) {
        sessionStorage.removeItem("navigatingFromGame");
        sessionStorage.removeItem("currentExerciseId");
        sessionStorage.removeItem("currentPuzzleIndex");
      }
    };
  }, []);

  // Get filters from URL parameters
  const selectedTheme = searchParams.get("theme") || "";
  const selectedLevel = searchParams.get("level")
    ? parseInt(searchParams.get("level")!)
    : null;

  // Função para embaralhar array
  const shuffleArray = <T,>(array: T[]): T[] => {
    const shuffled = [...array];
    for (let i = shuffled.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
    }
    return shuffled;
  };

  const exerciciosFiltrados = useMemo(() => {
    const filtered = allPuzzles.filter((puzzle) => {
      const themeMatch = !selectedTheme || puzzle.theme === selectedTheme;
      const levelMatch = !selectedLevel || puzzle.level === selectedLevel;
      return themeMatch && levelMatch;
    });

    // Não embaralhar se estivermos procurando por um exercício específico (vindo de uma partida)
    const exerciseId = searchParams.get("exerciseId");
    if (exerciseId || navigatingFromGame) {
      return filtered; // Retornar sem embaralhar
    }

    return shuffleArray(filtered);
  }, [
    allPuzzles,
    selectedTheme,
    selectedLevel,
    searchParams,
    navigatingFromGame,
  ]);

  // Corrigir índice do exercício atual se necessário
  useEffect(() => {
    if (currentPuzzle >= exerciciosFiltrados.length) {
      setCurrentPuzzle(0);
    }
    // eslint-disable-next-line
  }, [selectedTheme, selectedLevel]);

  // Atualizar exercício ao trocar filtro
  useEffect(() => {
    if (exerciciosFiltrados.length > 0) {
      carregarExercicio(
        exerciciosFiltrados[currentPuzzle] || exerciciosFiltrados[0]
      );
    } else {
      // Quando não há exercícios filtrados, mostrar posição inicial
      const initialGame = new Chess();
      setGame(initialGame);
      setExpectedMoves([]);
      setFirstMoveIsBlack(false);
      setResult({
        status: "info",
        message: "Nenhum exercício encontrado para este tema e nível.",
      });
      setCurrentPuzzle(0);
    }
    // eslint-disable-next-line
  }, [currentPuzzle, exerciciosFiltrados.length]);

  // Processar parâmetro exerciseId da URL para navegar para exercício específico
  useEffect(() => {
    const exerciseId = searchParams.get("exerciseId");
    if (exerciseId && allPuzzles.length > 0 && exerciciosFiltrados.length > 0) {
      // Definir que estamos navegando de uma partida e persistir no sessionStorage
      setNavigatingFromGame(true);
      sessionStorage.setItem("navigatingFromGame", "true");
      sessionStorage.setItem("currentExerciseId", exerciseId);

      // Tentar encontrar o puzzle na lista de puzzles filtrados (sem embaralhamento)
      const targetPuzzleIndex = exerciciosFiltrados.findIndex(
        (puzzle) => puzzle.id === exerciseId
      );

      if (targetPuzzleIndex !== -1) {
        // Puzzle encontrado na lista filtrada atual - navegar para ele
        setCurrentPuzzle(targetPuzzleIndex);
        sessionStorage.setItem(
          "currentPuzzleIndex",
          targetPuzzleIndex.toString()
        );
      } else {
        // Puzzle não está na lista filtrada atual - ir para o primeiro puzzle da lista
        setCurrentPuzzle(0);
        sessionStorage.setItem("currentPuzzleIndex", "0");
      }

      // Limpar o parâmetro exerciseId da URL após processar
      const newParams = new URLSearchParams(searchParams);
      newParams.delete("exerciseId");
      navigate({ search: newParams.toString() }, { replace: true });
    }
    // eslint-disable-next-line
  }, [allPuzzles, exerciciosFiltrados, searchParams, navigate]);

  // Restaurar estado quando voltar da partida
  useEffect(() => {
    if (navigatingFromGame && !searchParams.get("exerciseId")) {
      const savedExerciseId = sessionStorage.getItem("currentExerciseId");
      const savedPuzzleIndex = sessionStorage.getItem("currentPuzzleIndex");

      if (
        savedExerciseId &&
        savedPuzzleIndex &&
        exerciciosFiltrados.length > 0
      ) {
        const targetPuzzleIndex = parseInt(savedPuzzleIndex);
        // Verificar se o puzzle ainda existe na mesma posição
        if (
          targetPuzzleIndex < exerciciosFiltrados.length &&
          exerciciosFiltrados[targetPuzzleIndex]?.id === savedExerciseId
        ) {
          setCurrentPuzzle(targetPuzzleIndex);
        } else {
          // Tentar encontrar o puzzle na lista atual
          const foundIndex = exerciciosFiltrados.findIndex(
            (puzzle) => puzzle.id === savedExerciseId
          );
          if (foundIndex !== -1) {
            setCurrentPuzzle(foundIndex);
          }
        }
      }
    }
  }, [navigatingFromGame, searchParams, exerciciosFiltrados]);

  const carregarExercicio = useCallback((puzzle: PuzzleInterface) => {
    const novoGame = new Chess();

    // Carregar posição inicial usando FEN do puzzle
    if (puzzle.fen) {
      novoGame.load(puzzle.fen);
    } else if (
      puzzle.beforeExerciseMoves &&
      puzzle.beforeExerciseMoves.length > 0
    ) {
      // Quando FEN é nulo, usar beforeExerciseMoves para montar a posição inicial
      try {
        for (const move of puzzle.beforeExerciseMoves) {
          novoGame.move(move);
        }
      } catch (error) {
        console.error("Erro ao aplicar movimentos beforeExercise:", error);
        // Em caso de erro, volta para posição inicial
        novoGame.reset();
      }
    }

    // Usar movimentos esperados do puzzle
    setExpectedMoves(puzzle.expectedMoves);

    // Verificar se o primeiro lance é das negras
    setFirstMoveIsBlack(puzzle.firstMove === "black");

    setGame(novoGame);
    setCurrentMove(0);
    setResult({ status: "info", message: "Sua vez de jogar!" });
    setCustomizedSquare(null);
    setSelectedPiece(null);
    setPossibleMoves([]);
  }, []);

  const mostrarDica = () => {
    const newShowHint = !showHint;
    setShowHint(newShowHint);
    // Fechar os outros se estiver abrindo a dica
    if (newShowHint) {
      setShowEvaluationBar(false);
      setShowExplanation(false);
    }
  };

  const fazerMovimentoAutomatico = () => {
    if (currentMove < expectedMoves.length - 1) {
      try {
        // Criar um novo jogo e carregar a posição inicial
        const novoGame = new Chess();
        const currentPuzzleData = exerciciosFiltrados[currentPuzzle];
        if (currentPuzzleData.fen) {
          novoGame.load(currentPuzzleData.fen);
        } else if (
          currentPuzzleData.beforeExerciseMoves &&
          currentPuzzleData.beforeExerciseMoves.length > 0
        ) {
          // Quando FEN é nulo, usar beforeExerciseMoves para montar a posição inicial
          try {
            for (const move of currentPuzzleData.beforeExerciseMoves) {
              novoGame.move(move);
            }
          } catch (error) {
            console.error("Erro ao aplicar movimentos beforeExercise:", error);
            novoGame.reset();
          }
        }

        // Aplicar todos os movimentos até o movimento atual (incluindo o movimento do usuário)
        const movimentosAteAgora = expectedMoves.slice(0, currentMove + 1);
        for (const movimento of movimentosAteAgora) {
          novoGame.move(movimento.move);
        }

        // Pegar o próximo movimento do sistema
        // Posições ímpares (1, 3, 5...) são respostas automáticas do sistema
        const proximoMovimento = expectedMoves[currentMove + 1]?.move;

        // Tentar fazer o movimento diretamente do PGN
        try {
          novoGame.move(proximoMovimento);
          setGame(novoGame);
          setCurrentMove(currentMove + 2); // Incrementa em 2 pois já aplicamos o movimento do usuário
          setResult({ status: "info", message: "Sua vez de jogar!" });
        } catch (e) {
          // Se falhar, tentar fazer o movimento usando a notação algébrica completa
          const movimentosPossiveis = novoGame.moves({ verbose: true });
          console.log("Movimentos possíveis:", movimentosPossiveis);

          // Tentar encontrar o movimento que corresponde ao esperado
          const movimentoEncontrado = movimentosPossiveis.find((m) => {
            const mNormalizado = normalizarMovimento(m.san);
            const proximoNormalizado = normalizarMovimento(
              proximoMovimento || ""
            );
            console.log("Comparando:", mNormalizado, "com", proximoNormalizado);
            return mNormalizado === proximoNormalizado;
          });

          if (movimentoEncontrado) {
            novoGame.move(movimentoEncontrado);
            setGame(novoGame);
            setCurrentMove(currentMove + 2); // Incrementa em 2 pois já aplicamos o movimento do usuário
            setResult({ status: "info", message: "Sua vez de jogar!" });
          } else {
            throw new Error("Movimento não encontrado");
          }
        }
      } catch (error) {
        setResult({
          status: "error",
          message: "Erro ao processar movimento. Tente novamente.",
        });
      }
    }
  };

  const onNext = () => {
    if (currentPuzzle < exerciciosFiltrados.length - 1) {
      setCustomizedSquare(null);
      setSelectedPiece(null);
      setPossibleMoves([]);
      setCurrentPuzzle(currentPuzzle + 1);
      carregarExercicio(exerciciosFiltrados[currentPuzzle + 1]);
    } else {
      setCurrentPuzzle(0);
      setSelectedPiece(null);
      setPossibleMoves([]);
      carregarExercicio(exerciciosFiltrados[0]);
    }
  };

  const onPrev = () => {
    if (currentPuzzle > 0) {
      setCustomizedSquare(null);
      setSelectedPiece(null);
      setPossibleMoves([]);
      setCurrentPuzzle(currentPuzzle - 1);
      carregarExercicio(exerciciosFiltrados[currentPuzzle - 1]);
    }
  };

  const onViewGame = () => {
    if (exerciciosFiltrados[currentPuzzle]?.id) {
      // Marcar que estamos navegando de um puzzle para uma partida
      setNavigatingFromGame(true);
      sessionStorage.setItem("navigatingFromGame", "true");
      sessionStorage.setItem(
        "currentExerciseId",
        exerciciosFiltrados[currentPuzzle].id
      );
      sessionStorage.setItem("currentPuzzleIndex", currentPuzzle.toString());

      navigate(`/partida/${exerciciosFiltrados[currentPuzzle].id}`);
    }
  };

  const onAnalyze = () => {
    const newShowEvaluationBar = !showEvaluationBar;
    setShowEvaluationBar(newShowEvaluationBar);
    // Fechar os outros se estiver abrindo o engine
    if (newShowEvaluationBar) {
      setShowHint(false);
      setShowExplanation(false);
    }
  };

  const onExplain = () => {
    const newShowExplanation = !showExplanation;
    setShowExplanation(newShowExplanation);
    // Fechar os outros se estiver abrindo a explicação
    if (newShowExplanation) {
      setShowEvaluationBar(false);
      setShowHint(false);
    }
  };

  const handlePuzzleUpdate = async () => {
    setRefreshKey((prev) => prev + 1);
    // Atualizar os puzzles em memória para refletir as mudanças
    await refetch();
    // Fechar os campos de edição após salvar
    setShowHintEdit(false);
    setShowExplanationEdit(false);
    // Fechar visualizações se estiverem abertas
    setShowHint(false);
    setShowExplanation(false);
  };

  const handleEditHint = () => {
    setShowHintEdit(!showHintEdit);
    if (showExplanationEdit) {
      setShowExplanationEdit(false);
    }
    // Fechar dica e explicação ao abrir edição
    setShowHint(false);
    setShowExplanation(false);
  };

  const handleEditExplanation = () => {
    setShowExplanationEdit(!showExplanationEdit);
    if (showHintEdit) {
      setShowHintEdit(false);
    }
    // Fechar dica e explicação ao abrir edição
    setShowHint(false);
    setShowExplanation(false);
  };

  const handleDefineExerciseEnd = () => {
    setShowExerciseEndModal(true);
  };

  const handleDeletePuzzle = () => {
    setShowDeleteModal(true);
  };

  const handleModalSuccess = async () => {
    // Atualizar a lista de puzzles após operação bem-sucedida
    await refetch();
    setRefreshKey((prev) => prev + 1);
  };

  const handleApplyFilters = (theme: string, level: number | null) => {
    const newParams = new URLSearchParams();
    if (theme) newParams.set("theme", theme);
    if (level) newParams.set("level", level.toString());

    // Resetar o estado de navegação de partida quando aplicar filtros manualmente
    setNavigatingFromGame(false);
    sessionStorage.removeItem("navigatingFromGame");
    sessionStorage.removeItem("currentExerciseId");
    sessionStorage.removeItem("currentPuzzleIndex");

    navigate(`/puzzle?${newParams.toString()}`, { replace: true });
  };

  const handleOpenFilterModal = () => {
    setShowFilterModal(true);
  };

  const onRepeat = () => {
    // Resetar o exercício atual
    setCustomizedSquare(null);
    setSelectedPiece(null);
    setPossibleMoves([]);
    setResult(setInitialResult());
    setCurrentMove(0);
    carregarExercicio(exerciciosFiltrados[currentPuzzle]);
  };

  const onDrop = (sourceSquare: Square, targetSquare: Square) => {
    try {
      // Criar uma cópia do jogo atual para testar o movimento
      const gameCopy = new Chess(game.fen());
      const move = gameCopy.move({
        from: sourceSquare,
        to: targetSquare,
        promotion: "q",
      });

      if (move === null) return false;

      // Verificar se o movimento corresponde ao esperado
      const movimentoAtualSan = move.san;

      // Calcular o índice do movimento esperado
      // Posições pares (0, 2, 4...) são lances do usuário
      const indiceMovimentoEsperado = currentMove;
      const movimentoEsperado = expectedMoves[indiceMovimentoEsperado]?.move;

      if (!movimentoEsperado) {
        console.error("Movimento esperado não encontrado");
        return false;
      }

      const movimentoAtualNormalizado = normalizarMovimento(movimentoAtualSan);
      const movimentoEsperadoNormalizado = normalizarMovimento(
        movimentoEsperado || ""
      );

      if (movimentoAtualNormalizado === movimentoEsperadoNormalizado) {
        // Movimento correto, atualizar o jogo real
        setGame(gameCopy);
        setResult({ status: "info", message: "Sua vez de jogar!" });
        setCurrentMove(currentMove + 1);
        setCustomizedSquare(null);
        // Fechar dica e explicação ao fazer o lance
        setShowHint(false);
        setShowExplanation(false);

        // Verificar se é o último movimento do usuário
        const ehUltimoMovimentoUsuario =
          indiceMovimentoEsperado === expectedMoves.length - 1;

        if (ehUltimoMovimentoUsuario) {
          // Último movimento correto
          setResult({
            status: "success",
            message: "Acertou! Clique ao lado para continuar.",
          });
          // Adicionar um pequeno delay antes de mostrar o verde
          setTimeout(() => {
            setCustomizedSquare({
              square: targetSquare,
              variant: CustomizedSquareVariant.success,
            });
          }, 200);
        } else {
          // Movimento correto, mas ainda não é o último
          setResult({
            status: "info",
            message: "Boa! Mas ainda não acabou...",
          });
          // Fazer o movimento automático do oponente
          setTimeout(fazerMovimentoAutomatico, 400);
        }
      } else {
        // Movimento incorreto, mas mantemos no tabuleiro
        setGame(gameCopy);
        setCustomizedSquare({
          square: targetSquare,
          variant: CustomizedSquareVariant.error,
        });
        // Fechar dica e explicação ao fazer o lance (mesmo incorreto)
        setShowHint(false);
        setShowExplanation(false);
        setTimeout(() => {
          setResult({
            status: "error",
            message: "Errou, clique ao lado para tentar novamente.",
          });
        }, 100);
      }

      return true;
    } catch (e) {
      setTimeout(() => {
        setResult({
          status: "error",
          message: "Calma veloz! Jogada ilegal.",
        });
      }, 100);
      return false;
    }
  };

  const tryAgain = () => {
    const currentPuzzleData = exerciciosFiltrados[currentPuzzle];
    if (!currentPuzzleData) return;

    // Reconstruir o jogo até o ponto antes do erro
    const novoGame = new Chess();

    // Carregar posição inicial usando FEN do puzzle
    if (currentPuzzleData.fen) {
      novoGame.load(currentPuzzleData.fen);
    } else if (
      currentPuzzleData.beforeExerciseMoves &&
      currentPuzzleData.beforeExerciseMoves.length > 0
    ) {
      // Quando FEN é nulo, usar beforeExerciseMoves para montar a posição inicial
      try {
        for (const move of currentPuzzleData.beforeExerciseMoves) {
          novoGame.move(move);
        }
      } catch (error) {
        console.error("Erro ao aplicar movimentos beforeExercise:", error);
        novoGame.reset();
      }
    }

    // Aplicar movimentos até o ponto atual (antes do erro)
    for (let i = 0; i < currentMove && i < expectedMoves.length; i++) {
      try {
        novoGame.move(expectedMoves[i].move);
      } catch (error) {
        console.error(
          `Erro ao reaplicar movimento ${expectedMoves[i].move}:`,
          error
        );
        break;
      }
    }

    setGame(novoGame);
    setCustomizedSquare(null);
    setSelectedPiece(null);
    setPossibleMoves([]);
    setResult({ status: "info", message: "Sua vez de jogar!" });
  };

  const onSquareClick = (square: Square) => {
    // Se não houver peça selecionada e houver uma peça no quadrado clicado
    if (!selectedPiece && game.get(square)) {
      const movimentos = game.moves({ square, verbose: true });
      if (movimentos.length > 0) {
        setSelectedPiece(square);
        setPossibleMoves(movimentos.map((m) => m.to as Square));
      }
      return;
    }

    // Se já houver uma peça selecionada
    if (selectedPiece) {
      // Se clicar na mesma peça, deseleciona
      if (square === selectedPiece) {
        setSelectedPiece(null);
        setPossibleMoves([]);
        return;
      }

      // Se clicar em um movimento possível
      if (possibleMoves.includes(square)) {
        onDrop(selectedPiece, square);
        setSelectedPiece(null);
        setPossibleMoves([]);
        return;
      }

      // Se clicar em outra peça da mesma cor
      const pecaAtual = game.get(selectedPiece);
      const pecaClicada = game.get(square);
      if (pecaClicada && pecaAtual && pecaClicada.color === pecaAtual.color) {
        const movimentos = game.moves({ square, verbose: true });
        if (movimentos.length > 0) {
          setSelectedPiece(square);
          setPossibleMoves(movimentos.map((m) => m.to as Square));
        }
        return;
      }

      // Se clicar em qualquer outro lugar, deseleciona
      setSelectedPiece(null);
      setPossibleMoves([]);
    }
  };

  if (isLoading) {
    return (
      <div className="App Loading">
        <Loading />
      </div>
    );
  }

  if (allPuzzles.length === 0) {
    return (
      <div className="App">
        <div className="no-puzzles-container">
          <div className="no-puzzles-icon">🧩</div>
          <h3>Carregando puzzles...</h3>
          <p>Aguarde enquanto buscamos os exercícios</p>
        </div>
      </div>
    );
  }

  return (
    <AppTemplate title={firstMoveIsBlack ? "Jogam as pretas" : "Jogam as brancas"} withChessboard>
      <div className="App">
        {exerciciosFiltrados.length === 0 ? (
          <div className="no-results-container">
            <div className="no-results-icon">🔍</div>
            <h3>Nenhum puzzle encontrado</h3>
            <p>Não foram encontrados exercícios para os filtros selecionados</p>
            <div className="filter-suggestions">
              <button
                className="suggestion-button"
                onClick={() => navigate("/puzzle-selector")}
              >
                Voltar para seleção
              </button>
            </div>
          </div>
        ) : (
          <>
            <div className="chessboard-integrated-container">
              <div className="chessboard-container">
                <Chessboard
                  game={game}
                  boardOrientation={firstMoveIsBlack ? "black" : "white"}
                  disabled={false}
                  customizedSquare={customizedSquare}
                  selectedPiece={selectedPiece}
                  possibleMoves={possibleMoves}
                  onSquareClick={onSquareClick}
                  onDrop={onDrop}
                />
              </div>
              <ChessboardControls
                current={currentPuzzle}
                total={exerciciosFiltrados.length}
                result={result}
                onPrev={onPrev}
                onNext={onNext}
                onRetry={tryAgain}
                onHint={mostrarDica}
                firstMoveIsBlack={firstMoveIsBlack}
                onViewGame={onViewGame}
                onRepeat={onRepeat}
                onAnalyze={onAnalyze}
                onExplain={onExplain}
                mode="puzzle"
                isEngineActive={showEvaluationBar}
                isHintActive={showHint}
                isExplanationActive={showExplanation}
                exerciseCounter={`${currentPuzzle + 1} de ${
                  exerciciosFiltrados.length
                }`}
                puzzleLevel={exerciciosFiltrados[currentPuzzle]?.level || 1}
                selectedTheme={exerciciosFiltrados[currentPuzzle]?.theme || ""}
              />
            </div>
          </>
        )}
        {showEvaluationBar && (
          <div className="evaluation-container">
            <StockfishEvaluation
              fen={game.fen()}
              barMode={false}
              orientation="horizontal"
              boardSize={360}
              autoEvaluate={true}
              depth={12}
            />
          </div>
        )}

        {/* Hint display */}
        {exerciciosFiltrados.length > 0 && showHint && (
          <div className="hint-container">
            <div className="hint-text">
              <strong>Dica:</strong>{" "}
              {(() => {
                // Pegar a dica do movimento atual primeiro
                const currentMoveHint = expectedMoves[currentMove]?.hint;
                const puzzleHint = exerciciosFiltrados[currentPuzzle]?.hint;
                const hintText = currentMoveHint || puzzleHint;

                return (
                  <>
                    {hintText || "Nenhuma dica disponível para este puzzle."}
                    {currentMoveHint && (
                      <small
                        style={{
                          opacity: 0.7,
                          display: "block",
                          marginTop: "4px",
                        }}
                      >
                        (Dica do movimento atual)
                      </small>
                    )}
                  </>
                );
              })()}
            </div>
          </div>
        )}

        {/* Explanation for all users */}
        {exerciciosFiltrados.length > 0 && showExplanation && (
          <div className="explanation-container">
            <div className="explanation-content">
              {(() => {
                // Pegar a explicação do movimento atual primeiro
                const currentMoveExplanation =
                  expectedMoves[currentMove]?.explanation;
                const puzzleExplanationText =
                  exerciciosFiltrados[currentPuzzle]?.explanationText;
                const puzzleExplanationUrl =
                  exerciciosFiltrados[currentPuzzle]?.explanationUrl;

                const explanationText =
                  currentMoveExplanation || puzzleExplanationText;

                return (
                  <>
                    {explanationText && (
                      <div className="explanation-text">
                        <strong>Explicação:</strong> {explanationText}
                        {currentMoveExplanation && (
                          <small
                            style={{
                              opacity: 0.7,
                              display: "block",
                              marginTop: "4px",
                            }}
                          >
                            (Explicação do movimento atual)
                          </small>
                        )}
                      </div>
                    )}
                    {puzzleExplanationUrl?.includes("youtube") && (
                      <div className="video-container">
                        <iframe
                          width="100%"
                          height="315"
                          src={`https://www.youtube.com/embed/${puzzleExplanationUrl
                            ?.split("/")
                            .pop()}`}
                          title="Explicação em vídeo"
                          style={{ border: 0 }}
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowFullScreen
                        ></iframe>
                      </div>
                    )}
                    {!explanationText && !puzzleExplanationUrl && (
                      <div className="explanation-text">
                        <strong>Explicação:</strong> Nenhuma explicação
                        disponível para este puzzle.
                      </div>
                    )}
                  </>
                );
              })()}
            </div>
          </div>
        )}

        {/* Admin hint editor */}
        {exerciciosFiltrados.length > 0 && showHintEdit && isAdmin() && (
          <AdminEdit
            key={`hint-${currentPuzzle}-${refreshKey}`}
            puzzleId={exerciciosFiltrados[currentPuzzle]?.id || ""}
            type="hint"
            currentValue={exerciciosFiltrados[currentPuzzle]?.hint || ""}
            onUpdate={handlePuzzleUpdate}
            alwaysOpen={true}
          />
        )}

        {/* Admin explanation editor */}
        {exerciciosFiltrados.length > 0 && showExplanationEdit && isAdmin() && (
          <AdminEdit
            key={`explanation-${currentPuzzle}-${refreshKey}`}
            puzzleId={exerciciosFiltrados[currentPuzzle]?.id || ""}
            type="explanation"
            currentValue={
              exerciciosFiltrados[currentPuzzle]?.explanationText || ""
            }
            currentVideoUrl={
              exerciciosFiltrados[currentPuzzle]?.explanationUrl || ""
            }
            onUpdate={handlePuzzleUpdate}
            alwaysOpen={true}
          />
        )}

        {/* Admin buttons - always visible for admin users */}
        {exerciciosFiltrados.length > 0 && isAdmin() && (
          <AdminButtons
            onEditHint={handleEditHint}
            onEditExplanation={handleEditExplanation}
            onDefineExerciseEnd={handleDefineExerciseEnd}
            onDeletePuzzle={handleDeletePuzzle}
          />
        )}

        {/* Filter Button */}
        {exerciciosFiltrados.length > 0 && (
          <div className="filter-button-container">
            <button className="filter-button" onClick={handleOpenFilterModal}>
              Aplicar filtros
            </button>
          </div>
        )}

        {/* Exercise End Modal */}
        {showExerciseEndModal && exerciciosFiltrados.length > 0 && (
          <ExerciseEndModal
            puzzle={exerciciosFiltrados[currentPuzzle]}
            onClose={() => setShowExerciseEndModal(false)}
            onSuccess={handleModalSuccess}
          />
        )}

        {/* Delete Puzzle Modal */}
        {showDeleteModal && exerciciosFiltrados.length > 0 && (
          <DeletePuzzleModal
            puzzle={exerciciosFiltrados[currentPuzzle]}
            onClose={() => setShowDeleteModal(false)}
            onSuccess={handleModalSuccess}
          />
        )}

        {/* Filter Modal */}
        <PuzzleFilterModal
          isOpen={showFilterModal}
          onClose={() => setShowFilterModal(false)}
          onApplyFilters={handleApplyFilters}
          initialTheme={selectedTheme}
          initialLevel={selectedLevel}
        />
      </div>
    </AppTemplate>
  );
};

export default Puzzle;
