import { useState, useEffect, useCallback } from 'react';
import { stockfishService, StockfishEvaluation } from '../services/stockfish-service';

interface UseStockfishEvaluationState {
  evaluation: StockfishEvaluation | null;
  loading: boolean;
  error: string | null;
}

interface UseStockfishEvaluationResult extends UseStockfishEvaluationState {
  evaluatePosition: (fen: string, depth?: number) => Promise<void>;
  getBestMove: (fen: string, depth?: number) => Promise<string | null>;
  formatEvaluation: () => string;
  getEvaluationColor: () => 'white' | 'black' | 'equal';
  clearEvaluation: () => void;
}

export const useStockfishEvaluation = (): UseStockfishEvaluationResult => {
  const [state, setState] = useState<UseStockfishEvaluationState>({
    evaluation: null,
    loading: false,
    error: null,
  });

  const evaluatePosition = useCallback(async (fen: string, depth: number = 15) => {
    setState(prev => ({ ...prev, loading: true, error: null }));

    try {
      const evaluation = await stockfishService.evaluatePosition(fen, depth);
      setState({
        evaluation,
        loading: false,
        error: null,
      });
    } catch (error) {
      setState({
        evaluation: null,
        loading: false,
        error: error instanceof Error ? error.message : 'Unknown error occurred',
      });
    }
  }, []);

  const getBestMove = useCallback(async (fen: string, depth: number = 15): Promise<string | null> => {
    try {
      return await stockfishService.getBestMove(fen, depth);
    } catch (error) {
      console.error('Error getting best move:', error);
      return null;
    }
  }, []);

  const formatEvaluation = useCallback((): string => {
    if (!state.evaluation) return '--';
    return stockfishService.formatEvaluation(state.evaluation);
  }, [state.evaluation]);

  const getEvaluationColor = useCallback((): 'white' | 'black' | 'equal' => {
    if (!state.evaluation) return 'equal';
    return stockfishService.getEvaluationColor(state.evaluation);
  }, [state.evaluation]);

  const clearEvaluation = useCallback(() => {
    setState({
      evaluation: null,
      loading: false,
      error: null,
    });
  }, []);

  return {
    ...state,
    evaluatePosition,
    getBestMove,
    formatEvaluation,
    getEvaluationColor,
    clearEvaluation,
  };
};

// Hook para avaliação automática quando a posição muda
export const useAutoStockfishEvaluation = (
  fen: string | null,
  autoEvaluate: boolean = true,
  depth: number = 15
): UseStockfishEvaluationResult => {
  const stockfish = useStockfishEvaluation();

  useEffect(() => {
    if (autoEvaluate && fen && fen.trim() !== '') {
      stockfish.evaluatePosition(fen, depth);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fen, autoEvaluate, depth]);

  return stockfish;
};