import React, { useState, useMemo } from "react";
import classNames from "classnames";
import { usePuzzles } from "../../contexts/PuzzleContext";
import Styles from "./puzzle-filter-modal.module.scss";

interface PuzzleFilterModalProps {
  isOpen: boolean;
  onClose: () => void;
  onApplyFilters: (theme: string, level: number | null) => void;
  initialTheme?: string;
  initialLevel?: number | null;
}

const PuzzleFilterModal: React.FC<PuzzleFilterModalProps> = ({
  isOpen,
  onClose,
  onApplyFilters,
  initialTheme = "",
  initialLevel = null,
}) => {
  const { puzzles: allPuzzles } = usePuzzles();
  
  const [selectedTheme, setSelectedTheme] = useState<string>(initialTheme);
  const [selectedLevel, setSelectedLevel] = useState<number | null>(initialLevel);

  const themes = useMemo(() => {
    return Array.from(new Set(allPuzzles.map((puzzle) => puzzle.theme)));
  }, [allPuzzles]);

  const levels = useMemo(() => {
    // Sempre mostrar todos os 5 níveis, independente dos puzzles disponíveis
    return [1, 2, 3, 4, 5];
  }, []);

  const availablePuzzlesCount = useMemo(() => {
    return allPuzzles.filter((puzzle) => {
      const themeMatch = !selectedTheme || puzzle.theme === selectedTheme;
      const levelMatch = !selectedLevel || puzzle.level === selectedLevel;
      return themeMatch && levelMatch;
    }).length;
  }, [allPuzzles, selectedTheme, selectedLevel]);

  const handleApply = () => {
    onApplyFilters(selectedTheme, selectedLevel);
    onClose();
  };

  const renderStars = (level: number) => {
    return Array.from({ length: level }, (_, i) => (
      <span
        key={i}
        className={classNames(Styles.Star, Styles.StarFilled)}
      >
        ★
      </span>
    ));
  };

  if (!isOpen) return null;

  return (
    <div className={Styles.Overlay} onClick={onClose}>
      <div className={Styles.Modal} onClick={(e) => e.stopPropagation()}>
        <div className={Styles.Header}>
          <h2 className={Styles.Title}>Filtros de Exercícios</h2>
          <button className={Styles.CloseButton} onClick={onClose}>×</button>
        </div>

        <div className={Styles.Content}>
          <div className={Styles.FilterSection}>
            <label className={Styles.FilterLabel}>Tema</label>
            <select
              className={Styles.FilterSelect}
              value={selectedTheme}
              onChange={(e) => setSelectedTheme(e.target.value)}
            >
              <option value="">Todos os temas</option>
              {themes.map((theme) => (
                <option key={theme} value={theme}>
                  {theme}
                </option>
              ))}
            </select>
          </div>

          <div className={Styles.FilterSection}>
            <label className={Styles.FilterLabel}>Nível</label>
            <div className={Styles.LevelButtons}>
              <button
                className={classNames(Styles.LevelButton, {
                  [Styles.LevelButtonSelected]: selectedLevel === null
                })}
                onClick={() => setSelectedLevel(null)}
              >
                Todos
              </button>
              {levels.map((level) => (
                <button
                  key={level}
                  className={classNames(Styles.LevelButton, {
                    [Styles.LevelButtonSelected]: selectedLevel === level
                  })}
                  onClick={() => setSelectedLevel(level)}
                >
                  {renderStars(level)}
                </button>
              ))}
            </div>
          </div>

          <div className={Styles.ResultsSection}>
            <div className={Styles.PuzzleCount}>
              <span className={Styles.CountNumber}>{availablePuzzlesCount}</span>
              <span className={Styles.CountLabel}>
                {availablePuzzlesCount === 1 ? "puzzle" : "puzzles"}
              </span>
            </div>
            
            <button
              className={Styles.ApplyButton}
              onClick={handleApply}
              disabled={availablePuzzlesCount === 0}
            >
              Aplicar Filtros
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PuzzleFilterModal;