import React, { useState } from "react";
import { AppTemplate } from "../../components/app-template";
import { AdminUserList } from "../../components/admin-user-list/admin-user-list";
import { AdminUserDetails } from "../../components/admin-user-details/admin-user-details";
import { AdminUser } from "../../services/user-service";
import Styles from "./admin-home.module.scss";

export const AdminHome: React.FC = () => {
  const [selectedUser, setSelectedUser] = useState<AdminUser | null>(null);

  const handleUserSelect = (user: AdminUser) => {
    setSelectedUser(user);
  };

  const handleBack = () => {
    setSelectedUser(null);
  };

  const handleUserUpdated = (updatedUser: AdminUser) => {
    // Atualiza o usuário selecionado
    setSelectedUser(updatedUser);
  };

  return (
    <AppTemplate>
      <div className={Styles.AdminHomeContainer}>
        {selectedUser ? (
          <AdminUserDetails
            user={selectedUser}
            onBack={handleBack}
            onUserUpdated={handleUserUpdated}
          />
        ) : (
          <AdminUserList onUserSelect={handleUserSelect} />
        )}
      </div>
    </AppTemplate>
  );
};