export interface IResult {
  status: "success" | "error" | "info";
  message: string;
}

export function isFinalResult(result: IResult): boolean {
  return result.status === "success" || result.status === "error";
}

export function isSuccessResult(result: IResult): boolean {
  return result.status === "success";
}

export function isErrorResult(result: IResult): boolean {
  return result.status === "error";
}

export function setInitialResult(): IResult {
  return {
    status: "info",
    message: "Sua vez de jogar!",
  };
}
