import { useParams, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { Chess } from "chess.js";
import { Chessboard } from "../../components/chessboard";
import { gameService, IGame } from "../../services/game-service";
import { Loading } from "../../components/loading";
import { usePuzzles } from "../../contexts/PuzzleContext";
import { ChessboardControls } from "../../components/chessboard-controls/chessboard-controls";
import { StockfishEvaluation } from "../../components/stockfish-evaluation/stockfish-evaluation";
import { AppTemplate } from "../../components/app-template";
import whiteKingIcon from "../../icons/white-king2.svg";
import blackKingIcon from "../../icons/black-king2.svg";
import "./game.css";

export function Game() {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { puzzles } = usePuzzles();
  const [partida, setPartida] = useState<IGame | null>(null);
  const [game, setGame] = useState(new Chess());
  const [currentMove, setCurrentMove] = useState(0);
  const [moves, setMoves] = useState<string[]>([]);
  const [loading, setLoading] = useState(true);
  const [showEvaluationBar, setShowEvaluationBar] = useState(false);
  const [showExplanation, setShowExplanation] = useState(false);
  const [boardOrientation, setBoardOrientation] = useState<"white" | "black">(
    "white"
  );

  useEffect(() => {
    const fetchPartida = async () => {
      if (!id) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);

        // Primeiro, verificar se a partida existe no contexto de puzzles
        const puzzleFromContext = puzzles.find((puzzle) => puzzle.id === id);

        if (puzzleFromContext) {
          // Converter puzzle para formato IGame
          const gameFromPuzzle: IGame = {
            id: puzzleFromContext.id,
            white: puzzleFromContext.white,
            black: puzzleFromContext.black,
            event: puzzleFromContext.event || "Puzzle",
            date: puzzleFromContext.date || new Date().toISOString(),
            fen: puzzleFromContext.fen,
            completedGame: puzzleFromContext.completedGame || [],
          };

          setPartida(gameFromPuzzle);
          setMoves(gameFromPuzzle.completedGame as string[]);

          // Configurar posição inicial (FEN ou posição padrão)
          const initialGame = new Chess();
          if (gameFromPuzzle.fen) {
            try {
              console.log("Carregando FEN do contexto:", gameFromPuzzle.fen);
              initialGame.load(gameFromPuzzle.fen);
              console.log(
                "FEN carregado com sucesso. Posição atual:",
                initialGame.fen()
              );
            } catch (error) {
              console.error("Erro ao carregar FEN inicial:", error);
            }
          }

          setGame(initialGame);
          setCurrentMove(0);

          // Definir orientação inicial baseada no primeiro movimento
          setBoardOrientation(
            gameFromPuzzle.fen && new Chess(gameFromPuzzle.fen).turn() === "b"
              ? "black"
              : "white"
          );
        } else {
          // Fallback: fazer requisição para a API
          console.log("Partida não encontrada no contexto, buscando na API...");
          const response = await gameService.getGameById(id);

          if (response.success) {
            setPartida(response.data);
            setMoves(response.data.completedGame as string[]);

            // Configurar posição inicial (FEN ou posição padrão)
            const initialGame = new Chess();
            if (response.data.fen) {
              try {
                console.log("Carregando FEN da API:", response.data.fen);
                initialGame.load(response.data.fen);
                console.log(
                  "FEN carregado com sucesso. Posição atual:",
                  initialGame.fen()
                );
              } catch (error) {
                console.error("Erro ao carregar FEN inicial:", error);
              }
            }

            setGame(initialGame);
            setCurrentMove(0);

            // Definir orientação inicial baseada no primeiro movimento
            setBoardOrientation(
              response.data.fen && new Chess(response.data.fen).turn() === "b"
                ? "black"
                : "white"
            );
          } else {
            console.error(response.message);
          }
        }
      } catch (error) {
        console.error("Erro ao carregar partida:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchPartida();
  }, [id, puzzles]);

  const goToMove = (moveIndex: number) => {
    if (!partida || !moves) return;

    // Criar uma cópia do jogo atual
    const gameCopy = new Chess(game.fen());

    // Se estamos indo para trás, reconstituir desde o início
    if (moveIndex < currentMove) {
      // Reiniciar do FEN inicial ou posição padrão
      const freshGame = new Chess();
      if (partida.fen) {
        try {
          console.log("Reiniciando do FEN base:", partida.fen);
          freshGame.load(partida.fen);
        } catch (error) {
          console.error("Erro ao carregar FEN:", error);
          return;
        }
      }

      // Aplicar movimentos até o índice desejado
      for (let i = 0; i < moveIndex && i < moves.length; i++) {
        if (moves[i]) {
          try {
            console.log(`Reaplicando movimento ${i + 1}: ${moves[i]}`);
            freshGame.move(moves[i]);
          } catch (error) {
            console.error(`Erro ao reaplicar movimento ${moves[i]}:`, error);
            break;
          }
        }
      }

      console.log(
        `Posição reconstruída do lance ${moveIndex}:`,
        freshGame.fen()
      );
      setGame(freshGame);
    } else if (moveIndex > currentMove) {
      // Aplicar movimentos faltantes
      for (let i = currentMove; i < moveIndex && i < moves.length; i++) {
        if (moves[i]) {
          try {
            gameCopy.move(moves[i]);
          } catch (error) {
            console.error(`Erro ao aplicar movimento ${moves[i]}:`, error);
            break;
          }
        }
      }

      setGame(gameCopy);
    }

    setCurrentMove(moveIndex);
  };

  const onPrev = () => {
    if (currentMove > 0) {
      goToMove(currentMove - 1);
    }
  };

  const onNext = () => {
    if (moves && currentMove < moves.length) {
      goToMove(currentMove + 1);
    }
  };

  const onBackToExercise = () => {
    // Navegar de volta para o puzzle específico usando o ID da partida
    navigate(`/puzzle?exerciseId=${id}`);
  };

  const onFlipBoard = () => {
    setBoardOrientation(boardOrientation === "white" ? "black" : "white");
  };

  const toggleEvaluationBar = () => {
    setShowEvaluationBar(!showEvaluationBar);
  };

  const onExplain = () => {
    const newShowExplanation = !showExplanation;
    setShowExplanation(newShowExplanation);
    // Fechar os outros se estiver abrindo a explicação
    if (newShowExplanation) {
      setShowEvaluationBar(false);
    }
  };

  if (loading) {
    return (
      <div className="Loading">
        <Loading text="Carregando..." />
      </div>
    );
  }

  if (!partida) {
    return <div className="error">Partida não encontrada</div>;
  }

  return (
    <AppTemplate title="Partida" withChessboard withoutHeaderShadow>
      <div className="App">
        <div className="chessboard-integrated-container">
          <div className="chessboard-container">
            <Chessboard
              game={game}
              boardOrientation={boardOrientation}
              disabled={true}
              customizedSquare={null}
              selectedPiece={null}
              possibleMoves={[]}
              onSquareClick={() => {}}
              onDrop={() => false}
            />
          </div>
          <ChessboardControls
            current={currentMove}
            total={moves?.length || 0}
            onPrev={onPrev}
            onNext={onNext}
            onBackToExercise={onBackToExercise}
            onExplain={onExplain}
            onAnalyze={toggleEvaluationBar}
            onFlipBoard={onFlipBoard}
            mode="game"
            isEngineActive={showEvaluationBar}
            isExplanationActive={showExplanation}
          />
        </div>

        {showEvaluationBar && (
          <div className="evaluation-container">
            <StockfishEvaluation
              fen={game.fen()}
              // barMode={true}
              orientation="horizontal"
              boardSize={360}
              autoEvaluate={true}
              depth={15}
            />
          </div>
        )}

        {/* Explanation for all users */}
        {partida && showExplanation && (
          <div className="explanation-container">
            <div className="explanation-content">
              {(() => {
                // Buscar a explicação do puzzle correspondente
                const puzzleData = puzzles.find(
                  (puzzle) => puzzle.id === partida.id
                );
                const explanationText = puzzleData?.explanationText;
                const explanationUrl = puzzleData?.explanationUrl;

                return (
                  <>
                    {explanationText && (
                      <div className="explanation-text">
                        <strong>Explicação:</strong> {explanationText}
                      </div>
                    )}
                    {explanationUrl?.includes("youtube") && (
                      <div className="video-container">
                        <iframe
                          width="100%"
                          height="315"
                          src={`https://www.youtube.com/embed/${explanationUrl
                            ?.split("/")
                            .pop()}`}
                          title="Explicação em vídeo"
                          style={{ border: 0 }}
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowFullScreen
                        ></iframe>
                      </div>
                    )}
                    {!explanationText && !explanationUrl && (
                      <div className="explanation-text">
                        <strong>Explicação:</strong> Nenhuma explicação
                        disponível para esta partida.
                      </div>
                    )}
                  </>
                );
              })()}
            </div>
          </div>
        )}

        {/* Game Info */}
        <div className="game-info-container">
          <div className="game-info-content">
            <h3>Dados da Partida</h3>
            <div className="players-info">
              <div className="player-white">
                <img
                  src={whiteKingIcon}
                  alt="Rei branco"
                  className="king-icon"
                />
                <span>{partida.white || "Jogador desconhecido"}</span>
              </div>
              <div className="player-black">
                <img
                  src={blackKingIcon}
                  alt="Rei preto"
                  className="king-icon"
                />
                <span>{partida.black || "Jogador desconhecido"}</span>
              </div>
            </div>
            {partida.event && (
              <div className="game-event">
                <strong>Evento:</strong> {partida.event}
              </div>
            )}
            {partida.date && (
              <div className="game-date">
                <strong>Data:</strong> {partida.date}
              </div>
            )}
          </div>
        </div>
      </div>
    </AppTemplate>
  );
}
