import React, { useRef, useState } from "react";
import classNames from "classnames";
import { ReactComponent as PencilIcon } from "../../icons/pencil.svg";

import {
  generateUploadUrl,
  uploadImageToUrl,
  updateProfileImage,
} from "../../services/auth-service";
import { useAuth } from "../../contexts/AuthContext";
import Styles from "./profile-image-upload.module.scss";

interface ProfileImageUploadProps {
  readonly className?: string;
  readonly onUploadSuccess?: () => void;
  readonly onUploadError?: (error: string) => void;
}

export const ProfileImageUpload: React.FC<ProfileImageUploadProps> = ({
  className,
  onUploadSuccess,
  onUploadError,
}) => {
  const { user, refreshUserProfile } = useAuth();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [showOptionsModal, setShowOptionsModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const handleEditClick = () => {
    if (isUploading || isDeleting) return;
    setShowOptionsModal(true);
  };

  const handleUploadClick = () => {
    setShowOptionsModal(false);
    fileInputRef.current?.click();
  };

  const handleRemoveClick = () => {
    setShowOptionsModal(false);
    if (user?.profileImage) {
      setShowDeleteModal(true);
    }
  };

  const handleFileChange = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // Validar tipo de arquivo
    if (!file.type.startsWith("image/")) {
      onUploadError?.("Por favor, selecione apenas arquivos de imagem.");
      return;
    }

    // Validar tamanho (5MB máximo)
    const maxSize = 5 * 1024 * 1024; // 5MB
    if (file.size > maxSize) {
      onUploadError?.("A imagem deve ter no máximo 5MB.");
      return;
    }

    try {
      setIsUploading(true);

      // Gerar URL de upload do Cloudflare R2
      console.log("Gerando URL de upload para Cloudflare R2...");
      const fileName = `profile-${user?.id}-${Date.now()}.${file.name
        .split(".")
        .pop()}`;
      const uploadResponse = await generateUploadUrl({
        fileName,
        contentType: file.type,
      });

      // Upload direto para o Cloudflare R2
      console.log("Fazendo upload direto para Cloudflare R2...");
      await uploadImageToUrl(uploadResponse.uploadUrl, file);
      console.log("✅ Upload realizado com sucesso");

      // Atualizar perfil no backend
      await updateProfileImage({
        profileImage: uploadResponse.publicUrl,
      });

      // Refresh do perfil do usuário
      await refreshUserProfile();

      onUploadSuccess?.();
    } catch (error) {
      console.error("Erro no upload da imagem:", error);
      const errorMessage =
        error instanceof Error
          ? error.message
          : "Erro ao fazer upload da imagem. Tente novamente.";
      onUploadError?.(errorMessage);
    } finally {
      setIsUploading(false);
      // Limpar o input para permitir o mesmo arquivo novamente
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
    }
  };

  const handleDeleteConfirm = async () => {
    try {
      setIsDeleting(true);

      // Fazer PATCH com string vazia para remover a imagem
      await updateProfileImage({
        profileImage: "",
      });

      // Refresh do perfil do usuário
      await refreshUserProfile();

      setShowDeleteModal(false);
      onUploadSuccess?.();
    } catch (error) {
      console.error("Erro ao remover imagem:", error);
      const errorMessage =
        error instanceof Error
          ? error.message
          : "Erro ao remover imagem. Tente novamente.";
      onUploadError?.(errorMessage);
    } finally {
      setIsDeleting(false);
    }
  };

  const handleDeleteCancel = () => {
    setShowDeleteModal(false);
  };

  const handleOptionsCancel = () => {
    setShowOptionsModal(false);
  };

  return (
    <>
      <div className={classNames(Styles.ProfileImageContainer, className)}>
        <div className={Styles.ProfileImageWrapper}>
          {user?.profileImage ? (
            <img
              src={user.profileImage}
              alt="Foto de perfil"
              className={Styles.ProfileImage}
            />
          ) : (
            <div className={Styles.DefaultAvatar}>
              {user?.name?.charAt(0).toUpperCase() || "U"}
            </div>
          )}

          {/* Ícone de lápis para abrir modal de opções */}
          <div className={Styles.EditIcon} onClick={handleEditClick}>
            <PencilIcon />
          </div>
        </div>

        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          className={Styles.HiddenInput}
          disabled={isUploading || isDeleting}
        />
      </div>

      {/* Modal de opções */}
      {showOptionsModal && (
        <div className={Styles.ModalOverlay}>
          <div className={Styles.Modal}>
            <h3 className={Styles.ModalTitle}>Foto de perfil</h3>
            <p className={Styles.ModalMessage}>O que você gostaria de fazer?</p>
            <div className={Styles.ModalOptionsActions}>
              <button
                className={Styles.ModalUploadButton}
                onClick={handleUploadClick}
                disabled={isUploading || isDeleting}
              >
                {user?.profileImage ? "Alterar foto" : "Carregar foto"}
              </button>
              {user?.profileImage && (
                <button
                  className={Styles.ModalRemoveButton}
                  onClick={handleRemoveClick}
                  disabled={isUploading || isDeleting}
                >
                  Remover foto
                </button>
              )}
              <button
                className={Styles.ModalCancelButton}
                onClick={handleOptionsCancel}
                disabled={isUploading || isDeleting}
              >
                Cancelar
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal de confirmação de exclusão */}
      {showDeleteModal && (
        <div className={Styles.ModalOverlay}>
          <div className={Styles.Modal}>
            <h3 className={Styles.ModalTitle}>Remover foto de perfil</h3>
            <p className={Styles.ModalMessage}>
              Tem certeza de que deseja remover sua foto de perfil?
            </p>
            <div className={Styles.ModalActions}>
              <button
                className={Styles.ModalCancelButton}
                onClick={handleDeleteCancel}
                disabled={isDeleting}
              >
                Cancelar
              </button>
              <button
                className={Styles.ModalDeleteButton}
                onClick={handleDeleteConfirm}
                disabled={isDeleting}
              >
                {isDeleting ? "Removendo..." : "Remover"}
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};
