import React, { useState, useMemo, useEffect, useCallback } from "react";
import { useLocation } from "react-router-dom";
import { Chess, Square } from "chess.js";
import classNames from "classnames";
import { Chessboard } from "../../components/chessboard";
import { ChessboardControls } from "../../components/chessboard-controls/chessboard-controls";
import { StockfishEvaluation } from "../../components/stockfish-evaluation/stockfish-evaluation";
import { gameService, GameMove } from "../../services/game-service";
import { AppTemplate } from "../../components/app-template";
import Styles from "./free-analysis.module.scss";

interface SaveModalData {
  white: string;
  black: string;
  event: string;
  date: string;
  result: string;
}

export const FreeAnalysis: React.FC = () => {
  const location = useLocation();
  const [game, setGame] = useState(() => new Chess());
  const [selectedPiece, setSelectedPiece] = useState<Square | null>(null);
  const [moves, setMoves] = useState<string[]>([]);
  const [currentMoveIndex, setCurrentMoveIndex] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalData, setModalData] = useState<SaveModalData>({
    white: "",
    black: "",
    event: "",
    date: new Date().toISOString().split("T")[0],
    result: "*",
  });
  const [isSaving, setIsSaving] = useState(false);
  const [showEvaluationBar, setShowEvaluationBar] = useState(false);
  const [isLoadingGame, setIsLoadingGame] = useState(false);
  const [loadedGameId, setLoadedGameId] = useState<string | null>(null);
  const [boardOrientation, setBoardOrientation] = useState<"white" | "black">("white");

  // Função para extrair moves do PGN
  const parsePGNMoves = (pgn: string): string[] => {
    if (!pgn) return [];

    try {
      // Remove headers do PGN
      const pgnContent = pgn.replace(/\[.*?\]/g, "").trim();

      // Remove resultado do final (1-0, 0-1, 1/2-1/2, *)
      const cleanPgn = pgnContent
        .replace(/\s*(1-0|0-1|1\/2-1\/2|\*)\s*$/, "")
        .trim();

      // Extrai apenas os moves, removendo números dos lances
      const moveMatches = cleanPgn.match(
        /\d+\.\s*([a-h1-8NBRQKX+#=\-O]+)\s*([a-h1-8NBRQKX+#=\-O]+)?/g
      );

      if (!moveMatches) return [];

      const moves: string[] = [];

      moveMatches.forEach((match) => {
        const parts = match.split(/\d+\.\s*/)[1];
        if (parts) {
          const [whiteMove, blackMove] = parts.trim().split(/\s+/);
          if (whiteMove) moves.push(whiteMove);
          if (blackMove) moves.push(blackMove);
        }
      });

      return moves;
    } catch (error) {
      console.error("Erro ao analisar PGN:", error);
      return [];
    }
  };

  // Função para carregar jogo do backend
  const loadGameFromId = useCallback(async (gameId: string) => {
    setIsLoadingGame(true);
    try {
      const gameResult = await gameService.getGameById(gameId);

      if (gameResult.success && gameResult.data) {
        const gameData = gameResult.data;

        // Extrair moves do campo moves da nova API
        let gameMoves: string[] = [];

        if (gameData.moves && gameData.moves.length > 0) {
          // Nova estrutura: array de objetos {move: "e4"}
          gameMoves = gameData.moves.map(m => m.move);
        } else if (gameData.completedGame && gameData.completedGame.length > 0) {
          // Estrutura antiga: array de strings
          gameMoves = [...gameData.completedGame];
        } else if (gameData.pgn) {
          // Tentar extrair do PGN se moves estiver vazio
          gameMoves = parsePGNMoves(gameData.pgn);
        }

        // Aplicar moves ao tabuleiro
        const newGame = new Chess();
        const validMoves: string[] = [];

        try {
          gameMoves.forEach((move) => {
            const result = newGame.move(move);
            if (result) {
              validMoves.push(result.san);
            }
          });

          setGame(new Chess()); // Posição inicial
          setMoves(validMoves);
          setCurrentMoveIndex(0); // Índice inicial

          // Preencher dados do modal com informações do jogo
          setModalData({
            white: gameData.white || "",
            black: gameData.black || "",
            event: gameData.event || "",
            date: gameData.date
              ? gameData.date.split("T")[0]
              : new Date().toISOString().split("T")[0],
            result: "*",
          });


          setLoadedGameId(gameId);
        } catch (moveError) {
          console.error("Erro ao aplicar moves:", moveError);
          alert("Erro ao carregar os lances do jogo.");
        }
      } else {
        console.error("Erro ao carregar jogo:", gameResult.message);
        alert("Erro ao carregar jogo: " + gameResult.message);
      }
    } catch (error) {
      console.error("Erro ao carregar jogo:", error);
      alert("Erro ao carregar jogo.");
    } finally {
      setIsLoadingGame(false);
    }
  }, []);

  // Effect para carregar jogo se gameId estiver na URL
  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const gameId = searchParams.get("gameId");

    if (gameId && gameId !== loadedGameId) {
      loadGameFromId(gameId);
    }
  }, [location.search, loadedGameId, loadGameFromId]);

  const possibleMoves = useMemo(() => {
    if (!selectedPiece) return [];
    try {
      return game
        .moves({ square: selectedPiece, verbose: true })
        .map((move) => move.to);
    } catch (error) {
      return [];
    }
  }, [game, selectedPiece]);

  const handleSquareClick = (square: Square) => {
    if (selectedPiece) {
      if (selectedPiece === square) {
        setSelectedPiece(null);
        return;
      }

      try {
        const move = game.move({
          from: selectedPiece,
          to: square,
          promotion: "q",
        });

        if (move) {
          // Se estamos no meio da partida (não na última posição), cortar os lances futuros
          const newMoves =
            currentMoveIndex < moves.length
              ? [...moves.slice(0, currentMoveIndex), move.san]
              : [...moves, move.san];

          setMoves(newMoves);
          setCurrentMoveIndex(newMoves.length);
          setGame(new Chess(game.fen()));
          setSelectedPiece(null);
        } else {
          // Lance inválido, selecionar nova peça se houver uma peça válida no quadrado
          const piece = game.get(square);
          if (piece && piece.color === game.turn()) {
            setSelectedPiece(square);
          } else {
            setSelectedPiece(null);
          }
        }
      } catch (error) {
        // Lance impossível, tentar selecionar nova peça
        const piece = game.get(square);
        if (piece && piece.color === game.turn()) {
          setSelectedPiece(square);
        } else {
          setSelectedPiece(null);
        }
      }
    } else {
      const piece = game.get(square);
      if (piece && piece.color === game.turn()) {
        setSelectedPiece(square);
      }
    }
  };

  const handlePieceDrop = (
    sourceSquare: Square,
    targetSquare: Square
  ): boolean => {
    try {
      const move = game.move({
        from: sourceSquare,
        to: targetSquare,
        promotion: "q",
      });

      if (move) {
        // Se estamos no meio da partida (não na última posição), cortar os lances futuros
        const newMoves =
          currentMoveIndex < moves.length
            ? [...moves.slice(0, currentMoveIndex), move.san]
            : [...moves, move.san];

        setMoves(newMoves);
        setCurrentMoveIndex(newMoves.length);
        setGame(new Chess(game.fen()));
        setSelectedPiece(null);
        return true;
      }

      return false;
    } catch (error) {
      // Lance impossível, não fazer nada
      return false;
    }
  };

  const goToMove = (moveIndex: number) => {
    const newGame = new Chess();
    const movesToApply = moves.slice(0, moveIndex);

    try {
      movesToApply.forEach((move) => {
        newGame.move(move);
      });

      setGame(newGame);
      setCurrentMoveIndex(moveIndex);
      setSelectedPiece(null);
    } catch (error) {
      console.error("Erro ao aplicar movimentos:", error);
    }
  };

  const handlePrev = () => {
    if (currentMoveIndex > 0) {
      // Se estivermos na posição final (após todos os lances), remover o último lance
      if (currentMoveIndex === moves.length) {
        const newMoves = moves.slice(0, -1);
        setMoves(newMoves);
        setCurrentMoveIndex(newMoves.length);

        const newGame = new Chess();
        try {
          newMoves.forEach((move) => {
            newGame.move(move);
          });
          setGame(newGame);
        } catch (error) {
          console.error("Erro ao reconstruir posição:", error);
        }
      } else {
        // Caso contrário, apenas navegar para o lance anterior
        goToMove(currentMoveIndex - 1);
      }
      setSelectedPiece(null);
    }
  };

  const handleNext = () => {
    if (currentMoveIndex < moves.length) {
      goToMove(currentMoveIndex + 1);
    }
  };

  const handleReset = () => {
    setGame(new Chess());
    setMoves([]);
    setCurrentMoveIndex(0);
    setSelectedPiece(null);
    setLoadedGameId(null);

    // Limpar dados do modal
    const defaultModalData = {
      white: "",
      black: "",
      event: "",
      date: new Date().toISOString().split("T")[0],
      result: "*",
    };
    setModalData(defaultModalData);
  };

  const toggleEvaluationBar = () => {
    setShowEvaluationBar(!showEvaluationBar);
  };

  const handleFlipBoard = () => {
    setBoardOrientation(boardOrientation === "white" ? "black" : "white");
  };

  const handleSave = () => {
    if (moves.length === 0) {
      alert("Faça pelo menos um lance antes de salvar!");
      return;
    }
    setIsModalOpen(true);
  };

  const handleModalSubmit = async () => {
    if (!modalData.white || !modalData.black) {
      alert("Preencha pelo menos os nomes dos jogadores!");
      return;
    }

    setIsSaving(true);

    try {
      const pgn = generatePGN();

      // Converter moves para o formato esperado pela API
      const gameMoves: GameMove[] = moves.map((move) => ({
        move: move,
      }));

      // Salvar jogo via API
      const gameResult = await gameService.saveGame({
        moves: gameMoves,
        white: modalData.white,
        black: modalData.black,
        event: modalData.event || "Análise Livre",
        date: modalData.date,
        pgn: pgn,
      });

      if (gameResult.success) {
        alert("Análise salva com sucesso!");
        setIsModalOpen(false);
        handleReset();
        setModalData({
          white: "",
          black: "",
          event: "",
          date: new Date().toISOString().split("T")[0],
          result: "*",
        });
      } else {
        throw new Error(gameResult.message);
      }
    } catch (error) {
      console.error("Erro ao salvar análise:", error);
      alert("Erro ao salvar análise. Tente novamente.");
    } finally {
      setIsSaving(false);
    }
  };

  const handleCopyPGN = async () => {
    if (!modalData.white || !modalData.black) {
      alert("Preencha pelo menos os nomes dos jogadores!");
      return;
    }

    try {
      const pgn = generatePGN();
      
      // Tentar copiar para área de transferência
      await navigator.clipboard.writeText(pgn);
      alert("PGN copiado para a área de transferência!");
      
      setIsModalOpen(false);
      setModalData({
        white: "",
        black: "",
        event: "",
        date: new Date().toISOString().split("T")[0],
        result: "*",
      });
    } catch (error) {
      // Fallback para navegadores que não suportam clipboard API
      try {
        const pgn = generatePGN();
        const textArea = document.createElement("textarea");
        textArea.value = pgn;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand("copy");
        document.body.removeChild(textArea);

        alert("PGN copiado para a área de transferência!");
        
        setIsModalOpen(false);
        setModalData({
          white: "",
          black: "",
          event: "",
          date: new Date().toISOString().split("T")[0],
          result: "*",
        });
      } catch (fallbackError) {
        alert("Erro ao copiar PGN. Tente novamente.");
      }
    }
  };

  const generatePGN = (data: SaveModalData = modalData): string => {
    const headers = [
      `[White "${data.white}"]`,
      `[Black "${data.black}"]`,
      `[Event "${data.event || "Análise Livre"}"]`,
      `[Date "${data.date.replace(/-/g, ".")}"]`,
      `[Result "${data.result}"]`,
    ];

    const movesPGN = moves
      .reduce((acc, move, index) => {
        if (index % 2 === 0) {
          return acc + `${Math.floor(index / 2) + 1}. ${move} `;
        } else {
          return acc + `${move} `;
        }
      }, "")
      .trim();

    return headers.join("\n") + "\n\n" + movesPGN + " " + data.result;
  };


  return (
    <AppTemplate title="Análise" withChessboard withoutHeaderShadow>
      <div className="App">
        {isLoadingGame && (
          <div className={Styles.LoadingOverlay}>
            <div className={Styles.LoadingSpinner}></div>
            <p>Carregando jogo...</p>
          </div>
        )}
        {/* <div className="separator"></div> */}
        <div className="chessboard-integrated-container">
          <div className="chessboard-container">
            <Chessboard
              game={game}
              customizedSquare={null}
              selectedPiece={selectedPiece}
              possibleMoves={possibleMoves}
              boardOrientation={boardOrientation}
              disabled={isLoadingGame}
              onDrop={handlePieceDrop}
              onSquareClick={handleSquareClick}
            />
          </div>
          <ChessboardControls
            current={currentMoveIndex}
            total={moves.length}
            onPrev={handlePrev}
            onNext={handleNext}
            onAnalyze={toggleEvaluationBar}
            onReset={handleReset}
            onSave={handleSave}
            onFlipBoard={handleFlipBoard}
            mode="analysis"
            isEngineActive={showEvaluationBar}
          />
        </div>

        {showEvaluationBar && (
          <div className="evaluation-container">
            <StockfishEvaluation
              fen={game.fen()}
              barMode={false}
              orientation="horizontal"
              boardSize={360}
              autoEvaluate={true}
              depth={12}
            />
          </div>
        )}

        <div className={Styles.MovesSection}>
          <h3 className={Styles.MovesTitle}>Lances ({moves.length})</h3>
          <div className={Styles.MovesList}>
            {moves.length === 0 ? (
              <p className={Styles.NoMoves}>Nenhum lance realizado</p>
            ) : (
              <div className={Styles.MovesGrid}>
                {moves.map((move, index) => (
                  <span
                    key={index}
                    className={classNames(Styles.Move, {
                      [Styles.ActiveMove]: index === currentMoveIndex - 1,
                    })}
                    onClick={() => goToMove(index + 1)}
                  >
                    {index % 2 === 0 && (
                      <span className={Styles.MoveNumber}>
                        {Math.floor(index / 2) + 1}.
                      </span>
                    )}
                    <span className={Styles.MoveNotation}>{move}</span>
                  </span>
                ))}
              </div>
            )}
          </div>
        </div>

        {isModalOpen && (
          <div
            className={Styles.ModalOverlay}
            onClick={(e) => {
              if (e.target === e.currentTarget) {
                setIsModalOpen(false);
              }
            }}
          >
            <div className={Styles.Modal}>
              <h3 className={Styles.ModalTitle}>Salvar Análise</h3>

              <div className={Styles.FormGroup}>
                <label className={Styles.FormLabel}>
                  Jogador das Brancas *
                </label>
                <input
                  type="text"
                  className={Styles.FormInput}
                  value={modalData.white}
                  onChange={(e) =>
                    setModalData((prev) => ({ ...prev, white: e.target.value }))
                  }
                  placeholder="Nome do jogador das brancas"
                />
              </div>

              <div className={Styles.FormGroup}>
                <label className={Styles.FormLabel}>Jogador das Pretas *</label>
                <input
                  type="text"
                  className={Styles.FormInput}
                  value={modalData.black}
                  onChange={(e) =>
                    setModalData((prev) => ({ ...prev, black: e.target.value }))
                  }
                  placeholder="Nome do jogador das pretas"
                />
              </div>

              <div className={Styles.FormGroup}>
                <label className={Styles.FormLabel}>Evento</label>
                <input
                  type="text"
                  className={Styles.FormInput}
                  value={modalData.event}
                  onChange={(e) =>
                    setModalData((prev) => ({ ...prev, event: e.target.value }))
                  }
                  placeholder="Nome do evento (opcional)"
                />
              </div>

              <div className={Styles.FormGroup}>
                <label className={Styles.FormLabel}>Data</label>
                <input
                  type="date"
                  className={Styles.FormInput}
                  value={modalData.date}
                  onChange={(e) =>
                    setModalData((prev) => ({ ...prev, date: e.target.value }))
                  }
                />
              </div>

              <div className={Styles.FormGroup}>
                <label className={Styles.FormLabel}>Resultado</label>
                <select
                  className={Styles.FormInput}
                  value={modalData.result}
                  onChange={(e) =>
                    setModalData((prev) => ({
                      ...prev,
                      result: e.target.value,
                    }))
                  }
                >
                  <option value="*">Em andamento (*)</option>
                  <option value="1-0">Brancas vencem (1-0)</option>
                  <option value="0-1">Pretas vencem (0-1)</option>
                  <option value="1/2-1/2">Empate (1/2-1/2)</option>
                </select>
              </div>

              <div className={Styles.ModalActions}>
                <button
                  className={classNames(
                    Styles.ModalButton,
                    Styles.CancelButton
                  )}
                  onClick={() => setIsModalOpen(false)}
                  disabled={isSaving}
                >
                  Cancelar
                </button>
                <button
                  className={classNames(
                    Styles.ModalButton,
                    Styles.ConfirmButton
                  )}
                  onClick={handleCopyPGN}
                  disabled={isSaving}
                >
                  Copiar PGN
                </button>
                <button
                  className={classNames(
                    Styles.ModalButton,
                    Styles.ConfirmButton
                  )}
                  onClick={handleModalSubmit}
                  disabled={isSaving}
                >
                  {isSaving ? "Salvando..." : "Salvar"}
                </button>
              </div>
            </div>
          </div>
        )}

      </div>
    </AppTemplate>
  );
};
