import React, { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import classNames from 'classnames';
import { verifyEmail } from '../../services/auth-service';
import { EmailVerificationResponse } from '../../domain/models/user';
import Styles from './verify-email.module.scss';

export const VerifyEmail: React.FC = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
  const [message, setMessage] = useState<string>('');
  const [isVerifying, setIsVerifying] = useState<boolean>(true);

  useEffect(() => {
    const token = searchParams.get('token');
    
    if (!token) {
      setStatus('error');
      setMessage('Token de verificação não encontrado na URL');
      setIsVerifying(false);
      return;
    }

    handleVerification(token);
  }, [searchParams]);

  const handleVerification = async (token: string) => {
    try {
      setIsVerifying(true);
      const response: EmailVerificationResponse = await verifyEmail({ token });
      
      if (response.success) {
        setStatus('success');
        setMessage(response.message || 'E-mail verificado com sucesso!');
      } else {
        setStatus('error');
        setMessage(response.error || 'Erro ao verificar e-mail');
      }
    } catch (error) {
      setStatus('error');
      setMessage('Erro interno. Tente novamente mais tarde.');
    } finally {
      setIsVerifying(false);
    }
  };

  const handleGoToLogin = () => {
    navigate('/login');
  };

  const renderContent = () => {
    if (isVerifying) {
      return (
        <div className={Styles.VerificationContent}>
          <div className={Styles.LoadingSpinner}></div>
          <h2 className={Styles.VerificationTitle}>Verificando e-mail...</h2>
          <p className={Styles.VerificationMessage}>
            Aguarde enquanto validamos seu e-mail.
          </p>
        </div>
      );
    }

    if (status === 'success') {
      return (
        <div className={Styles.VerificationContent}>
          <div className={Styles.SuccessIcon}>✓</div>
          <h2 className={Styles.VerificationTitle}>E-mail Verificado!</h2>
          <p className={Styles.VerificationMessage}>
            {message}
          </p>
          <button 
            onClick={handleGoToLogin}
            className={classNames(Styles.ActionButton, Styles.ActionButtonPrimary)}
          >
            Fazer Login
          </button>
        </div>
      );
    }

    return (
      <div className={Styles.VerificationContent}>
        <div className={Styles.ErrorIcon}>✕</div>
        <h2 className={Styles.VerificationTitle}>Verificação Falhou</h2>
        <p className={Styles.VerificationMessage}>
          {message}
        </p>
        <div className={Styles.ActionButtons}>
          <button 
            onClick={handleGoToLogin}
            className={classNames(Styles.ActionButton, Styles.ActionButtonSecondary)}
          >
            Ir para Login
          </button>
        </div>
      </div>
    );
  };

  return (
    <div className={Styles.VerifyEmailContainer}>
      <div className={Styles.VerifyEmailCard}>
        {renderContent()}
      </div>
    </div>
  );
};