import { useState } from "react";
import { Puzzle } from "../../domain/models/puzzle";
import { deletePuzzle } from "../../services/puzzle-service";
import Styles from "./delete-puzzle-modal.module.scss";
import classNames from "classnames";

interface DeletePuzzleModalProps {
  puzzle: Puzzle;
  onClose: () => void;
  onSuccess: () => void;
}

export function DeletePuzzleModal({ puzzle, onClose, onSuccess }: DeletePuzzleModalProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleConfirm = async () => {
    setIsLoading(true);
    setError(null);

    try {
      await deletePuzzle(puzzle.id);
      onSuccess();
      onClose();
    } catch (err) {
      setError("Erro ao excluir o puzzle. Tente novamente.");
      console.error("Error deleting puzzle:", err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    onClose();
  };

  return (
    <div className={Styles.ModalOverlay}>
      <div className={Styles.ModalContainer}>
        <div className={Styles.ModalHeader}>
          <h3>Excluir Puzzle</h3>
        </div>

        <div className={Styles.ModalContent}>
          <div className={Styles.WarningIcon}>⚠️</div>
          <div className={Styles.WarningText}>
            <p>Tem certeza que deseja excluir este puzzle?</p>
            <p><strong>Esta ação não pode ser desfeita.</strong></p>
          </div>

          <div className={Styles.PuzzleInfo}>
            <div className={Styles.InfoRow}>
              <span className={Styles.Label}>Tema:</span>
              <span className={Styles.Value}>{puzzle.theme}</span>
            </div>
            <div className={Styles.InfoRow}>
              <span className={Styles.Label}>Nível:</span>
              <span className={Styles.Value}>{"★".repeat(puzzle.level)}</span>
            </div>
            <div className={Styles.InfoRow}>
              <span className={Styles.Label}>Partida:</span>
              <span className={Styles.Value}>
                {puzzle.white.split(' ')[0]} vs {puzzle.black.split(' ')[0]}
              </span>
            </div>
          </div>

          {error && (
            <div className={Styles.ErrorMessage}>
              {error}
            </div>
          )}
        </div>

        <div className={Styles.ModalActions}>
          <button 
            className={classNames(Styles.Button, Styles.CancelButton)}
            onClick={handleCancel}
            disabled={isLoading}
          >
            Cancelar
          </button>
          <button 
            className={classNames(Styles.Button, Styles.DeleteButton)}
            onClick={handleConfirm}
            disabled={isLoading}
          >
            {isLoading ? "Excluindo..." : "Excluir"}
          </button>
        </div>
      </div>
    </div>
  );
}