import { Puzzle, PuzzleMove } from "../domain/models/puzzle";

const BASE_URL =
  "https://backend-acesso-total-production.up.railway.app/api/v1";

interface ApiResponse {
  success: boolean;
  data: Puzzle[];
  message: string;
}

interface CreatePuzzleRequest {
  theme: string;
  pgn: string;
}

interface CreatePuzzleResponse {
  success: boolean;
  message: string;
}

interface UpdatePuzzleRequest {
  hint?: string;
  explanationText?: string;
  explanationUrl?: string;
  expectedMoves?: PuzzleMove[];
}

interface UpdatePuzzleResponse {
  success: boolean;
  message: string;
  data?: Puzzle;
}

interface DeletePuzzleResponse {
  success: boolean;
  message: string;
}

export const fetchPuzzles = async (): Promise<Puzzle[]> => {
  try {
    const response = await fetch(`${BASE_URL}/puzzles`);

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const apiResponse: ApiResponse = await response.json();

    if (!apiResponse.success) {
      throw new Error(apiResponse.message || "API returned success: false");
    }

    if (!Array.isArray(apiResponse.data)) {
      throw new Error("API response data is not an array");
    }

    return apiResponse.data;
  } catch (error) {
    console.error("Error fetching puzzles:", error);
    return [];
  }
};

export const createPuzzlesFromPGN = async (
  puzzleData: CreatePuzzleRequest
): Promise<CreatePuzzleResponse> => {
  try {
    console.log("🚀 Enviando PGN para o backend:");
    console.log("📋 Tema:", puzzleData.theme);
    console.log("♟️ PGN:", puzzleData.pgn);
    console.log("📤 Payload completo:", JSON.stringify(puzzleData, null, 2));
    
    const response = await fetch(`${BASE_URL}/puzzles/convert`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(puzzleData),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: CreatePuzzleResponse = await response.json();
    
    console.log("✅ Resposta do backend recebida:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    console.log("💬 Mensagem:", result.message);
    console.log("📥 Resposta completa:", JSON.stringify(result, null, 2));

    return result;
  } catch (error) {
    console.error("🚨 Erro ao criar puzzle:");
    console.error("📋 Tema enviado:", puzzleData.theme);
    console.error("♟️ PGN enviado:", puzzleData.pgn);
    console.error("❌ Detalhes do erro:", error);
    throw new Error("Erro ao criar puzzle. Tente novamente.");
  }
};

export const updatePuzzle = async (
  puzzleId: string,
  updateData: UpdatePuzzleRequest
): Promise<UpdatePuzzleResponse> => {
  try {
    console.log("🔄 Atualizando puzzle:", puzzleId);
    console.log("📝 Dados para atualizar:", JSON.stringify(updateData, null, 2));

    const response = await fetch(`${BASE_URL}/puzzles/${puzzleId}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(updateData),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: UpdatePuzzleResponse = await response.json();
    
    console.log("✅ Puzzle atualizado com sucesso:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    console.log("💬 Mensagem:", result.message);

    return result;
  } catch (error) {
    console.error("🚨 Erro ao atualizar puzzle:");
    console.error("🆔 ID do puzzle:", puzzleId);
    console.error("📝 Dados enviados:", updateData);
    console.error("❌ Detalhes do erro:", error);
    throw new Error("Erro ao atualizar puzzle. Tente novamente.");
  }
};

export const deletePuzzle = async (
  puzzleId: string
): Promise<DeletePuzzleResponse> => {
  try {
    console.log("🗑️ Excluindo puzzle:", puzzleId);

    const response = await fetch(`${BASE_URL}/puzzles/${puzzleId}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: DeletePuzzleResponse = await response.json();
    
    console.log("✅ Puzzle excluído com sucesso:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    console.log("💬 Mensagem:", result.message);

    return result;
  } catch (error) {
    console.error("🚨 Erro ao excluir puzzle:");
    console.error("🆔 ID do puzzle:", puzzleId);
    console.error("❌ Detalhes do erro:", error);
    throw new Error("Erro ao excluir puzzle. Tente novamente.");
  }
};
