import { ReactComponent as ChevronLeftIcon } from "../../icons/chevron-left.svg";
import { ReactComponent as ChevronRightIcon } from "../../icons/chevron-right.svg";
import { ReactComponent as RefreshIcon } from "../../icons/refresh-error.svg";
import { ReactComponent as ChevronSuccessRightIcon } from "../../icons/chevron-success-right.svg";
import { ReactComponent as LightbulbIcon } from "../../icons/lightbulb-05.svg";
import { ReactComponent as PawnIcon } from "../../icons/pawn.svg";
import { ReactComponent as MicrochipIcon } from "../../icons/microchip.svg";
import { ReactComponent as RefreshRepeatIcon } from "../../icons/refresh.svg";
import { ReactComponent as BookIcon } from "../../icons/book.svg";
import { ReactComponent as PuzzleIcon } from "../../icons/puzzle.svg";
import { ReactComponent as SaveIcon } from "../../icons/save.svg";
import { ReactComponent as EraserIcon } from "../../icons/eraser.svg";
import { ReactComponent as RepeatIcon } from "../../icons/repeat.svg";
import classNames from "classnames";
import Styles from "./chessboard-controls.module.scss";
import {
  IResult,
  isErrorResult,
  isFinalResult,
} from "../../domain/models/result";

interface IChessboardControls {
  readonly current: number;
  readonly total: number;
  readonly result?: IResult;
  readonly firstMoveIsBlack?: boolean;
  readonly onPrev: () => void;
  readonly onNext: () => void;
  readonly onRetry?: () => void;
  readonly onHint?: () => void;
  readonly onViewGame?: () => void;
  readonly onRepeat?: () => void;
  readonly onAnalyze?: () => void;
  readonly onExplain?: () => void;
  readonly onBackToExercise?: () => void;
  readonly onReset?: () => void;
  readonly onSave?: () => void;
  readonly onDelete?: () => void;
  readonly onFlipBoard?: () => void;
  readonly mode?: "puzzle" | "game" | "analysis" | "opening";
  readonly integrated?: boolean;
  readonly isEngineActive?: boolean;
  readonly isHintActive?: boolean;
  readonly isExplanationActive?: boolean;
  readonly onMainline?: () => void;
  readonly ecoCode?: string;
  readonly exerciseCounter?: string;
  readonly puzzleLevel?: number;
  readonly selectedTheme?: string;
}

export function ChessboardControls({
  current,
  total,
  result,
  onPrev,
  onNext,
  onRetry,
  onHint,
  firstMoveIsBlack,
  onViewGame,
  onRepeat,
  onAnalyze,
  onExplain,
  onBackToExercise,
  onReset,
  onSave,
  onDelete,
  onFlipBoard,
  mode = "puzzle",
  integrated = false,
  isEngineActive = false,
  isHintActive = false,
  isExplanationActive = false,
  onMainline,
  ecoCode,
  exerciseCounter,
  puzzleLevel,
  selectedTheme,
}: IChessboardControls) {
  if (result && isFinalResult(result)) {
    return (
      <div
        className={classNames(Styles.Controls, Styles.SuccessControls, {
          [Styles.Integrated]: integrated,
        })}
      >
        <div className={Styles.SideButtonContainer}>
          <button
            className={classNames(Styles.ControlButton, Styles.SideButton)}
            onClick={onViewGame}
          >
            <PawnIcon />
          </button>
          <span className={Styles.SideButtonLabel}>Partida</span>
        </div>

        <button
          className={classNames(
            Styles.ControlButton,
            Styles.UnifiedFeedbackButton,
            {
              [Styles.ErrorButton]: isErrorResult(result),
              [Styles.SuccessButton]: !isErrorResult(result),
            }
          )}
          onClick={isErrorResult(result) ? onRetry : onNext}
        >
          {isErrorResult(result) ? "Repetir" : "Continuar"}
          {isErrorResult(result) ? (
            <RefreshIcon />
          ) : (
            <ChevronSuccessRightIcon />
          )}
        </button>

        {!isErrorResult(result) ? (
          <div className={Styles.SideButtonContainer}>
            <button
              className={classNames(Styles.ControlButton, Styles.SideButton)}
              onClick={onRepeat}
            >
              <RefreshRepeatIcon />
            </button>
            <span className={Styles.SideButtonLabel}>Repetir</span>
          </div>
        ) : (
          <div className={Styles.SideButtonContainer}>
            <button
              className={classNames(Styles.ControlButton, Styles.SideButton)}
              onClick={onHint}
            >
              <LightbulbIcon />
            </button>
            <span className={Styles.SideButtonLabel}>Dica</span>
          </div>
        )}
      </div>
    );
  }

  const renderPuzzleControls = () => (
    <>
      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onViewGame}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <PawnIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Partida</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isEngineActive,
        })}
      >
        <button
          onClick={onAnalyze}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isEngineActive,
          })}
        >
          <MicrochipIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Engine</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onPrev}
          disabled={current === 0}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronLeftIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Ant.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onNext}
          disabled={current === total - 1}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronRightIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Próx.</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isHintActive,
        })}
      >
        <button
          onClick={onHint}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isHintActive,
          })}
        >
          <LightbulbIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Dica</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isExplanationActive,
        })}
      >
        <button
          onClick={onExplain}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isExplanationActive,
          })}
        >
          <BookIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Explicação</span>
      </div>
    </>
  );

  const renderGameControls = () => (
    <>
      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onBackToExercise}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <PuzzleIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Exercício</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isEngineActive,
        })}
      >
        <button
          onClick={onAnalyze}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isEngineActive,
          })}
        >
          <MicrochipIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Engine</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onPrev}
          disabled={current === 0}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronLeftIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Ant.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onNext}
          disabled={current >= total}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronRightIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Próx.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onFlipBoard}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <RepeatIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Inverter</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isExplanationActive,
        })}
      >
        <button
          onClick={onExplain}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isExplanationActive,
          })}
        >
          <BookIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Explicação</span>
      </div>
    </>
  );

  const renderAnalysisControls = () => (
    <>
      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onReset}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <EraserIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Limpar</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isEngineActive,
        })}
      >
        <button
          onClick={onAnalyze}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isEngineActive,
          })}
        >
          <MicrochipIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Engine</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onPrev}
          disabled={current === 0}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronLeftIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Ant.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onNext}
          disabled={current >= total}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronRightIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Próx.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onFlipBoard}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <RepeatIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Inverter</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onSave}
          disabled={total === 0}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <SaveIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Salvar</span>
      </div>
    </>
  );

  const renderOpeningControls = () => (
    <>
      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onReset}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <RefreshRepeatIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Resetar</span>
      </div>

      <div
        className={classNames(Styles.SideButtonContainer, {
          [Styles.SideButtonContainerActive]: isEngineActive,
        })}
      >
        <button
          onClick={onAnalyze}
          className={classNames(Styles.ControlButton, Styles.SideButton, {
            [Styles.EngineActive]: isEngineActive,
          })}
        >
          <MicrochipIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Engine</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onPrev}
          disabled={current === 0}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronLeftIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Ant.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onNext}
          disabled={current >= total}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <ChevronRightIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Próx.</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onFlipBoard}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <RepeatIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Inverter</span>
      </div>

      <div className={Styles.SideButtonContainer}>
        <button
          onClick={onMainline}
          className={classNames(Styles.ControlButton, Styles.SideButton)}
        >
          <BookIcon />
        </button>
        <span className={Styles.SideButtonLabel}>Mainline</span>
      </div>
    </>
  );

  return (
    <div
      className={classNames(Styles.Controls, {
        [Styles.Integrated]: integrated,
      })}
    >
      <div
        className={classNames(Styles.ButtonsContainer, {
          [Styles.PuzzleMode]: mode === "puzzle",
          [Styles.GameMode]: mode === "game",
          [Styles.AnalysisMode]: mode === "analysis",
          [Styles.OpeningMode]: mode === "opening",
          [Styles.Integrated]: integrated,
        })}
      >
        {mode === "puzzle"
          ? renderPuzzleControls()
          : mode === "game"
          ? renderGameControls()
          : mode === "opening"
          ? renderOpeningControls()
          : renderAnalysisControls()}
      </div>
      
      {mode === "puzzle" && (selectedTheme || puzzleLevel) && (
        <div className={Styles.PuzzleInfo}>
          <div className={Styles.PuzzleHorizontalLayout}>
            <div className={Styles.ThemeDisplay}>
              {selectedTheme ? `Tema: ${selectedTheme}` : ""}
            </div>
            <div className={Styles.LevelDisplay}>
              {puzzleLevel ? "★".repeat(puzzleLevel) : ""}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
