export const login = async (credentials: LoginCredentials): Promise<AuthResponse> => {
  // Primeiro, verifica se é um usuário hardcoded (caio, rafael, admin)
  const hardcodedUser = MOCK_USERS.find(
    u => u.username === credentials.username && u.password === credentials.password
  );

  if (hardcodedUser) {
    await new Promise(resolve => setTimeout(resolve, 500));
    const { password, ...userWithoutPassword } = hardcodedUser;
    const mockToken = `mock_token_${hardcodedUser.id}_${Date.now()}`;
    
    localStorage.setItem('auth_token', mockToken);
    
    return {
      success: true,
      user: userWithoutPassword,
      token: mockToken,
    };
  }

  // Se não for hardcoded, tenta fazer login no backend
  try {
    console.log("🚀 Tentando login no backend para usuário:", credentials.username);
    
    const response = await fetch(`${BASE_URL}/auth/login`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: credentials.username, // Assume que username pode ser email
        password: credentials.password,
      }),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      
      if (response.status === 401) {
        return {
          success: false,
          message: "Email ou senha inválidos",
        };
      }
      
      if (response.status === 403) {
        return {
          success: false,
          message: "Conta não verificada. Verifique seu e-mail.",
        };
      }
      
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: AuthResponse = await response.json();
    
    console.log("✅ Login no backend bem-sucedido:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    
    if (result.success && result.token) {
      localStorage.setItem('auth_token', result.token);
      return result;
    }

    return {
      success: false,
      message: result.message || "Erro ao fazer login",
    };
  } catch (error) {
    console.error("🚨 Erro ao fazer login no backend:");
    console.error("👤 Usuário:", credentials.username);
    console.error("❌ Detalhes do erro:", error);
    
    // Fallback para comportamento mock
    return {
      success: false,
      message: "Usuário ou senha inválidos",
    };
  }
};

