export const validateToken = async (token: string): Promise<TokenValidationResponse> => {
  if (!token) {
    return {
      valid: false,
    };
  }
  
  // For mock tokens, use saved user data
  if (token.startsWith('mock_token_')) {
    await new Promise(resolve => setTimeout(resolve, 200));
    const savedUser = getCurrentUser();
    if (savedUser) {
      return {
        valid: true,
        user: savedUser,
      };
    }
    return {
      valid: false,
    };
  }
  
  // For real tokens, validate with backend
  try {
    console.log("🚀 Validando token com o backend");
    
    const response = await fetch(`${BASE_URL}/auth/validate`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`,
      },
      body: JSON.stringify({ token }),
    });

    if (!response.ok) {
      console.error("❌ Token inválido:", response.status, response.statusText);
      return {
        valid: false,
      };
    }

    const result = await response.json();
    
    console.log("✅ Token validado com sucesso");
    
    if (result.valid && result.user) {
      return {
        valid: true,
        user: result.user,
      };
    }
    
    return {
      valid: false,
    };
  } catch (error) {
    console.error("🚨 Erro ao validar token:", error);
    
    // Fallback: manter usuário salvo se existir
    const savedUser = getCurrentUser();
    if (savedUser) {
      console.log("⚠️ Usando dados salvos localmente como fallback");
      return {
        valid: true,
        user: savedUser,
      };
    }
    
    return {
      valid: false,
    };
  }
};

export const getCurrentUserProfile = async (): Promise<ProfileResponse | null> => {
  const token = localStorage.getItem('auth_token');
  
  if (!token) {
    return null;
  }

  // For mock tokens, use saved user data
  if (token.startsWith('mock_token_')) {
    const savedUser = getCurrentUser();
    
    if (savedUser) {
      const profile = {
        id: savedUser.id,
        hotmartId: savedUser.hotmartId || `hm_${savedUser.id}`,
        email: savedUser.email,
        name: savedUser.name,
        createdAt: savedUser.createdAt || new Date().toISOString(),
        updatedAt: savedUser.updatedAt || new Date().toISOString(),
        profileImage: savedUser.profileImage
      };
      return profile;
    }
    return null;
  }

  // For real tokens, make API call to backend /me endpoint
  try {
    console.log("🚀 Fazendo chamada para o endpoint /me do backend");
    
    const response = await fetch(`${BASE_URL}/auth/me`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`,
      },
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend /me:", response.status, response.statusText);
      
      if (response.status === 401) {
        // Token inválido, retornar null mas NÃO limpar dados aqui
        // O AuthContext decide se deve limpar ou não
        console.warn("⚠️ Token inválido no endpoint /me - retornando null");
        return null;
      }
      
      if (response.status === 404) {
        console.warn("⚠️ Usuário não encontrado no endpoint /me - retornando null");
        return null;
      }
      
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: MeResponse = await response.json();
    
    console.log("✅ Perfil do usuário obtido com sucesso do backend");
    console.log("📊 ID:", result.user.id);
    console.log("📧 Email:", result.user.email);
    console.log("👤 Nome:", result.user.name);
    
    // Converter formato MeResponse para ProfileResponse
    const profile: ProfileResponse = {
      id: result.user.id,
      hotmartId: result.user.id, // Usar ID como hotmartId temporariamente
      email: result.user.email,
      name: result.user.name,
      createdAt: result.user.createdAt,
      updatedAt: result.user.createdAt, // Usar createdAt como updatedAt se não disponível
      profileImage: result.user.profileImage
    };
    
    return profile;
  } catch (error) {
    console.error("🚨 Erro ao obter perfil do usuário:");
    console.error("❌ Detalhes do erro:", error);
    
    // Em caso de erro, verificar se temos dados salvos localmente
    const savedUser = getCurrentUser();
    if (savedUser && savedUser.hotmartId) {
      console.log("⚠️ Usando dados salvos localmente como fallback");
      return {
        id: savedUser.id,
        hotmartId: savedUser.hotmartId,
        email: savedUser.email,
        name: savedUser.name,
        createdAt: savedUser.createdAt || new Date().toISOString(),
        updatedAt: savedUser.updatedAt || new Date().toISOString(),
        profileImage: savedUser.profileImage
      };
    }
    
    return null;
  }
};

export const logout = async (): Promise<void> => {
  localStorage.removeItem('auth_token');
  localStorage.removeItem('user');
};

export const getCurrentUser = (): User | null => {
  const userData = localStorage.getItem('user');
  
  if (userData) {
    try {
      const parsedUser = JSON.parse(userData);
      return parsedUser;
    } catch (error) {
      console.error("❌ getCurrentUser - Erro ao parsear:", error);
      return null;
    }
  }
  return null;
};

export const saveUser = (user: User): void => {
  localStorage.setItem('user', JSON.stringify(user));
};

export const clearUser = (): void => {
  localStorage.removeItem('user');
  localStorage.removeItem('auth_token');
};

export const getAuthToken = (): string | null => {
  return localStorage.getItem('auth_token');
};

export const signup = async (credentials: SignupCredentials): Promise<SignupResponse> => {
  try {
    console.log("🚀 Enviando dados de cadastro para o backend:");
    console.log("📧 Email:", credentials.email);
    console.log("👤 Nome:", credentials.name);
    
    const response = await fetch(`${BASE_URL}/auth/signup`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(credentials),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      
      if (response.status === 400) {
        const errorData = await response.json();
        return {
          success: false,
          error: errorData.message || "Dados inválidos"
        };
      }
      
      if (response.status === 409) {
        return {
          success: false,
          error: "E-mail já cadastrado no sistema"
        };
      }
      
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: SignupResponse = await response.json();
    
    console.log("✅ Resposta do backend recebida:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    console.log("💬 Mensagem:", result.message);

    return result;
  } catch (error) {
    console.error("🚨 Erro ao criar conta:");
    console.error("📧 Email enviado:", credentials.email);
    console.error("❌ Detalhes do erro:", error);
    
    // Fallback para comportamento mock em caso de erro de rede
    return await signupMock(credentials);
  }
};

// Função mock como fallback
const signupMock = async (credentials: SignupCredentials): Promise<SignupResponse> => {
  await new Promise(resolve => setTimeout(resolve, 800));

  // Verifica se email já existe nos usuários ativos
  const existingUser = MOCK_USERS.find(user => user.email === credentials.email);
  if (existingUser) {
    return {
      success: false,
      error: "E-mail já cadastrado no sistema"
    };
  }

  // Verifica se email está pendente de verificação
  const pendingUser = PENDING_VERIFICATION.find(user => user.email === credentials.email);
  if (pendingUser) {
    return {
      success: false,
      error: "E-mail já possui cadastro pendente de verificação"
    };
  }

  // Gera token de verificação
  const verificationToken = `verify_${credentials.email}_${Date.now()}_${Math.random().toString(36).substring(2, 11)}`;

  // Adiciona usuário à lista de pendentes
  PENDING_VERIFICATION.push({
    email: credentials.email,
    password: credentials.password,
    name: credentials.name,
    token: verificationToken,
    createdAt: new Date().toISOString()
  });

  // Simula envio de email (em produção seria enviado aqui)
  const verificationUrl = `${window.location.origin}${window.location.pathname}#/verify-email?token=${verificationToken}`;
  console.log(`Mock: Email de verificação enviado para ${credentials.email}`);
  console.log(`Token: ${verificationToken}`);
  console.log(`URL de verificação: ${verificationUrl}`);

  return {
    success: true,
    message: "Cadastro realizado! Verifique seu e-mail para ativar a conta.",
    requiresEmailVerification: true
  };
};

export const verifyEmail = async (request: EmailVerificationRequest): Promise<EmailVerificationResponse> => {
  try {
    console.log("🚀 Enviando token de verificação para o backend:");
    console.log("🔑 Token:", request.token);
    
    const response = await fetch(`${BASE_URL}/auth/verify-email`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        token: request.token
      }),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      
      if (response.status === 400) {
        const errorData = await response.json();
        return {
          success: false,
          error: errorData.message || "Token inválido"
        };
      }
      
      if (response.status === 404) {
        return {
          success: false,
          error: "Token de verificação inválido ou expirado"
        };
      }
      
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: EmailVerificationResponse = await response.json();
    
    console.log("✅ Resposta do backend recebida:");
    console.log("📊 Status:", result.success ? "Sucesso" : "Erro");
    console.log("💬 Mensagem:", result.message);

    return result;
  } catch (error) {
    console.error("🚨 Erro ao verificar email:");
    console.error("🔑 Token enviado:", request.token);
    console.error("❌ Detalhes do erro:", error);
    
    // Fallback para comportamento mock em caso de erro de rede
    return await verifyEmailMock(request);
  }
};

// Função mock como fallback
const verifyEmailMock = async (request: EmailVerificationRequest): Promise<EmailVerificationResponse> => {
  await new Promise(resolve => setTimeout(resolve, 500));

  // Procura o usuário pendente pelo token
  const pendingUserIndex = PENDING_VERIFICATION.findIndex(user => user.token === request.token);
  
  if (pendingUserIndex === -1) {
    return {
      success: false,
      error: "Token de verificação inválido ou expirado"
    };
  }

  const pendingUser = PENDING_VERIFICATION[pendingUserIndex];
  
  // Cria novo usuário ativo
  const newUser = {
    id: `user_${Date.now()}_${Math.random().toString(36).substring(2, 8)}`,
    username: pendingUser.email, // Usa email como username
    password: pendingUser.password,
    email: pendingUser.email,
    name: pendingUser.name,
    role: "user" as const,
    hotmartId: `hm_${Date.now()}`,
    courseAccess: true,
    createdAt: pendingUser.createdAt,
    updatedAt: new Date().toISOString()
  };

  // Adiciona aos usuários ativos
  MOCK_USERS.push(newUser);
  
  // Remove da lista de pendentes
  PENDING_VERIFICATION.splice(pendingUserIndex, 1);

  return {
    success: true,
    message: "E-mail verificado com sucesso! Agora você pode fazer login."
  };
};

