export const uploadImageToUrl = async (uploadUrl: string, file: File): Promise<void> => {
  try {
    console.log("🚀 Fazendo upload da imagem:");
    console.log("🔗 URL:", uploadUrl);
    console.log("📁 Arquivo:", file.name);
    console.log("📏 Tamanho:", (file.size / 1024 / 1024).toFixed(2) + " MB");
    console.log("📎 Content-Type:", file.type);
    
    const response = await fetch(uploadUrl, {
      method: "PUT",
      body: file,
      mode: 'cors',
      headers: {
        'Content-Type': file.type,
      },
    });

    console.log("📊 Status da resposta:", response.status);
    console.log("📊 Headers da resposta:", Object.fromEntries(response.headers.entries()));

    if (!response.ok) {
      const responseText = await response.text();
      console.error("❌ Erro no upload da imagem:", response.status, response.statusText);
      console.error("❌ Resposta do servidor:", responseText);
      
      if (response.status === 403) {
        throw new Error("Erro 403: Acesso negado ao Cloudflare R2. Verifique se a URL de upload está válida e as permissões do bucket estão corretas.");
      }
      
      throw new Error(`Upload failed! status: ${response.status} - ${responseText}`);
    }

    console.log("✅ Upload da imagem realizado com sucesso");
  } catch (error) {
    console.error("🚨 Erro no upload da imagem:", error);
    
    // Se ainda houver erro de CORS, vamos tentar uma abordagem alternativa
    if (error instanceof Error && (error.message?.includes('CORS') || error.message?.includes('Access to fetch'))) {
      console.log("⚠️ Erro de CORS detectado, tentando método alternativo...");
      throw new Error("Erro de CORS: O backend precisa configurar as permissões do Cloudflare para permitir uploads diretos do frontend");
    }
    
    throw error;
  }
};


export const updateProfileImage = async (request: UpdateProfileImageRequest): Promise<UpdateProfileImageResponse> => {
  const token = localStorage.getItem('auth_token');
  
  if (!token) {
    return {
      success: false,
      error: "Token de autenticação não encontrado"
    };
  }

  // Get current user to obtain user ID
  const currentUser = getCurrentUser();
  if (!currentUser) {
    return {
      success: false,
      error: "Usuário não encontrado"
    };
  }

  try {
    console.log("🚀 Atualizando imagem de perfil no backend:");
    console.log("👤 ID do usuário:", currentUser.id);
    console.log("🖼️ URL da imagem:", request.profileImage);
    
    const response = await fetch(`${BASE_URL}/users/${currentUser.id}/profile-image`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`,
      },
      body: JSON.stringify(request),
    });

    if (!response.ok) {
      console.error("❌ Erro na resposta do backend:", response.status, response.statusText);
      
      if (response.status === 401) {
        return {
          success: false,
          error: "Token de autenticação inválido"
        };
      }
      
      if (response.status === 404) {
        return {
          success: false,
          error: "Usuário não encontrado"
        };
      }
      
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: UpdateProfileImageResponse = await response.json();
    
    console.log("✅ Imagem de perfil atualizada com sucesso");

    // Update local user data with new profile image
    if (result.success) {
      const updatedUser = { ...currentUser, profileImage: request.profileImage };
      saveUser(updatedUser);
    }

    return result;
  } catch (error) {
    console.error("🚨 Erro ao atualizar imagem de perfil:", error);
    throw error;
  }
};