import { Chess, Square } from "chess.js";
import { Chessboard as ReactChessboard } from "react-chessboard";
import Styles from "./chessboard.module.scss";
import {
  CustomizedSquareVariant,
  ICustomizedSquare,
} from "../../domain/models/customized-square";
import { Nullable } from "../../domain/models";
import { useEffect, useState } from "react";

interface IChessboardProps {
  readonly game: Chess;
  readonly customizedSquare: Nullable<ICustomizedSquare>;
  readonly selectedPiece: Square | null;
  readonly possibleMoves: ReadonlyArray<Square>;
  readonly boardOrientation: "white" | "black";
  readonly disabled: boolean;
  readonly onDrop: (sourceSquare: Square, targetSquare: Square) => boolean;
  readonly onSquareClick: (square: Square) => void;
  readonly customBoardWidth?: number;
}

export function Chessboard({
  game,
  disabled,
  boardOrientation,
  selectedPiece,
  possibleMoves,
  customizedSquare,
  onSquareClick,
  onDrop,
  customBoardWidth,
}: IChessboardProps) {
  const [boardWidth, setBoardWidth] = useState(customBoardWidth || 400);

  useEffect(() => {
    if (customBoardWidth) {
      setBoardWidth(customBoardWidth);
      return;
    }

    const updateBoardWidth = () => {
      const containerElement = document.querySelector(".chessboard-container");
      if (containerElement) {
        const containerWidth = containerElement.getBoundingClientRect().width;
        const width = Math.min(containerWidth, 400);
        setBoardWidth(width);
      } else {
        const width = Math.min(window.innerWidth - 32, 400);
        setBoardWidth(width);
      }
    };

    updateBoardWidth();
    window.addEventListener("resize", updateBoardWidth);

    return () => {
      window.removeEventListener("resize", updateBoardWidth);
    };
  }, [customBoardWidth]);

  if (disabled) {
    return (
      <div className={Styles.ChessboardWrapper}>
        <ReactChessboard
          position={game.fen()}
          boardWidth={boardWidth}
          arePiecesDraggable={false}
          boardOrientation={boardOrientation}
        />
      </div>
    );
  }

  return (
    <div className={Styles.ChessboardWrapper}>
      <ReactChessboard
        position={game.fen()}
        boardWidth={boardWidth}
        arePiecesDraggable={false}
        onPieceDrop={onDrop}
        areArrowsAllowed
        animationDuration={200}
        onSquareClick={onSquareClick}
        boardOrientation={boardOrientation}
        customSquareStyles={{
          ...(customizedSquare &&
            customizedSquare.variant === CustomizedSquareVariant.error && {
              [customizedSquare.square]: {
                backgroundColor: "rgba(255, 0, 0, 0.5)",
              },
            }),
          ...(customizedSquare &&
            customizedSquare.variant === CustomizedSquareVariant.success && {
              [customizedSquare.square]: {
                backgroundColor: "rgba(0, 255, 0, 0.4)",
              },
            }),
          ...(customizedSquare &&
            customizedSquare.variant === CustomizedSquareVariant.hint && {
              [customizedSquare.square]: {
                backgroundColor: "rgba(33, 150, 243, 0.4)",
              },
            }),
          ...(selectedPiece && {
            [selectedPiece]: {
              backgroundColor: "rgba(255, 255, 0, 0.4)",
            },
          }),
          ...possibleMoves.reduce(
            (acc, square) => ({
              ...acc,
              [square]: {
                background: `radial-gradient(circle, ${
                  game.get(selectedPiece!)?.color === "w"
                    ? "rgba(255, 255, 255, 0.4)"
                    : "rgba(0, 0, 0, 0.4)"
                } 25%, transparent 25%)`,
              },
            }),
            {}
          ),
        }}
      />
    </div>
  );
}
