import { useState } from "react";
import classNames from "classnames";
import { updatePuzzle } from "../../services/puzzle-service";
import Styles from "./admin-edit.module.scss";

interface AdminEditProps {
  puzzleId: string;
  type: "hint" | "explanation";
  currentValue?: string;
  currentVideoUrl?: string;
  onUpdate?: () => void;
  alwaysOpen?: boolean;
}

export function AdminEdit({ 
  puzzleId, 
  type, 
  currentValue = "", 
  currentVideoUrl = "",
  onUpdate,
  alwaysOpen = false
}: AdminEditProps) {
  const [isEditing, setIsEditing] = useState(alwaysOpen);
  const [textValue, setTextValue] = useState(currentValue);
  const [videoUrl, setVideoUrl] = useState(currentVideoUrl);
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState<{ type: "success" | "error"; text: string } | null>(null);

  const buttonText = type === "hint" ? "Editar Dica" : "Editar Explicação";

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setMessage(null);

    try {
      const updateData = type === "hint" 
        ? { hint: textValue }
        : { explanationText: textValue, explanationUrl: videoUrl };

      const result = await updatePuzzle(puzzleId, updateData);

      if (result.success) {
        setMessage({ type: "success", text: "Atualizado com sucesso!" });
        if (!alwaysOpen) {
          setIsEditing(false);
        }
        onUpdate?.();
      } else {
        setMessage({ type: "error", text: result.message || "Erro ao atualizar" });
      }
    } catch (error) {
      setMessage({ type: "error", text: "Erro ao atualizar. Tente novamente." });
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    setTextValue(currentValue);
    setVideoUrl(currentVideoUrl);
    if (!alwaysOpen) {
      setIsEditing(false);
    }
    setMessage(null);
  };

  if (!isEditing && !alwaysOpen) {
    return (
      <div className={Styles.AdminEditContainer}>
        <button
          className={Styles.AdminEditButton}
          onClick={() => setIsEditing(true)}
        >
          {buttonText}
        </button>
        {message && (
          <div className={classNames({
            [Styles.SuccessMessage]: message.type === "success",
            [Styles.ErrorMessage]: message.type === "error"
          })}>
            {message.text}
          </div>
        )}
      </div>
    );
  }

  return (
    <div className={Styles.AdminEditContainer}>
      <form onSubmit={handleSubmit} className={Styles.AdminEditForm}>
        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel}>
            {type === "hint" ? "Texto da Dica:" : "Texto da Explicação:"}
          </label>
          <textarea
            className={Styles.FormTextarea}
            value={textValue}
            onChange={(e) => setTextValue(e.target.value)}
            placeholder={type === "hint" 
              ? "Digite o texto da dica..." 
              : "Digite o texto da explicação..."
            }
            disabled={isLoading}
          />
        </div>

        {type === "explanation" && (
          <div className={Styles.FormGroup}>
            <label className={Styles.FormLabel}>
              URL do Vídeo (YouTube):
            </label>
            <input
              type="url"
              className={Styles.FormInput}
              value={videoUrl}
              onChange={(e) => setVideoUrl(e.target.value)}
              placeholder="https://www.youtube.com/watch?v=..."
              disabled={isLoading}
            />
          </div>
        )}

        <div className={Styles.FormActions}>
          {!alwaysOpen && (
            <button
              type="button"
              className={Styles.CancelButton}
              onClick={handleCancel}
              disabled={isLoading}
            >
              Cancelar
            </button>
          )}
          <button
            type="submit"
            className={Styles.SubmitButton}
            disabled={isLoading}
          >
            {isLoading ? "Salvando..." : "Salvar"}
          </button>
        </div>

        {isLoading && (
          <div className={Styles.LoadingText}>
            Atualizando puzzle...
          </div>
        )}
      </form>
    </div>
  );
}