import React, { useState } from "react";
import classNames from "classnames";
import { SignupCredentials, SignupResponse } from "../../domain/models/user";
import { signup } from "../../services/auth-service";
import logoImg from "../../images/logo.png";
import Styles from "./signup-form.module.scss";

interface SignupFormProps {
  onSignupSuccess?: (response: SignupResponse) => void;
  onBackToLogin?: () => void;
}

export const SignupForm: React.FC<SignupFormProps> = ({
  onSignupSuccess,
  onBackToLogin,
}) => {
  const [formData, setFormData] = useState<
    SignupCredentials & { confirmPassword: string }
  >({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  });
  const [error, setError] = useState<string>("");
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    if (error) setError("");
  };

  const validateForm = (): string | null => {
    if (!formData.name.trim()) {
      return "Nome é obrigatório";
    }
    if (!formData.email.trim()) {
      return "E-mail é obrigatório";
    }
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      return "E-mail inválido";
    }
    if (!formData.password) {
      return "Senha é obrigatória";
    }
    if (formData.password.length < 6) {
      return "Senha deve ter pelo menos 6 caracteres";
    }
    if (formData.password !== formData.confirmPassword) {
      return "Senhas não conferem";
    }
    return null;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const validationError = validateForm();
    if (validationError) {
      setError(validationError);
      return;
    }

    setIsLoading(true);
    setError("");

    try {
      const response = await signup({
        name: formData.name.trim(),
        email: formData.email.trim().toLowerCase(),
        password: formData.password,
      });

      if (response.success) {
        onSignupSuccess?.(response);
      } else {
        setError(response.error || "Erro ao realizar cadastro");
      }
    } catch (err) {
      setError("Erro interno. Tente novamente.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className={Styles.SignupContainer}>
      <form className={Styles.SignupForm} onSubmit={handleSubmit}>
        <div className={Styles.SignupLogo}>
          <img
            src={logoImg}
            alt="Acesso Total Chess"
            className={Styles.LogoImg}
          />
        </div>

        <div className={Styles.WelcomeMessage}>Criar Conta</div>
        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="name">
            Nome Completo
          </label>
          <input
            id="name"
            name="name"
            type="text"
            value={formData.name}
            onChange={handleInputChange}
            className={classNames(Styles.FormInput, {
              [Styles.InputError]: error && error.includes("Nome"),
            })}
            placeholder="Digite seu nome completo"
            disabled={isLoading}
            autoComplete="name"
          />
        </div>

        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="email">
            E-mail
          </label>
          <input
            id="email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleInputChange}
            className={classNames(Styles.FormInput, {
              [Styles.InputError]: error && error.includes("E-mail"),
            })}
            placeholder="Digite seu e-mail"
            disabled={isLoading}
            autoComplete="email"
          />
        </div>

        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="password">
            Senha
          </label>
          <input
            id="password"
            name="password"
            type="password"
            value={formData.password}
            onChange={handleInputChange}
            className={classNames(Styles.FormInput, {
              [Styles.InputError]: error && error.includes("Senha"),
            })}
            placeholder="Digite sua senha (mín. 6 caracteres)"
            disabled={isLoading}
            autoComplete="new-password"
          />
        </div>

        <div className={Styles.FormGroup}>
          <label className={Styles.FormLabel} htmlFor="confirmPassword">
            Confirmar Senha
          </label>
          <input
            id="confirmPassword"
            name="confirmPassword"
            type="password"
            value={formData.confirmPassword}
            onChange={handleInputChange}
            className={classNames(Styles.FormInput, {
              [Styles.InputError]: error && error.includes("Senhas"),
            })}
            placeholder="Digite sua senha novamente"
            disabled={isLoading}
            autoComplete="new-password"
          />
        </div>

        {error && <div className={Styles.ErrorMessage}>{error}</div>}

        <button
          type="submit"
          disabled={isLoading}
          className={classNames(Styles.SignupButton, {
            [Styles.SignupButtonLoading]: isLoading,
          })}
        >
          {isLoading ? "Criando conta..." : "Criar Conta"}
        </button>

        <div className={Styles.BackToLoginContainer}>
          <button
            type="button"
            onClick={onBackToLogin}
            className={Styles.BackToLoginLink}
            disabled={isLoading}
          >
            Já tenho conta - Fazer Login
          </button>
        </div>
      </form>
    </div>
  );
};
