import { getAuthToken } from "./auth-service";

const BASE_URL =
  "https://backend-acesso-total-production.up.railway.app/api/v1";

export interface IGame {
  readonly id: string;
  readonly white: string;
  readonly black: string;
  readonly event: string | null;
  readonly date: string | null;
  readonly fen: string | null;
  readonly completedGame: ReadonlyArray<string>;
  readonly moves?: ReadonlyArray<{move: string}>;
  readonly pgn?: string;
}

export interface GameMove {
  move: string;
  explanation?: string;
  hint?: string;
  annotation?: string;
}

export interface SaveGameRequest {
  moves: GameMove[];
  white: string;
  black: string;
  event?: string;
  date?: string;
  pgn?: string;
}

interface ApiResponse {
  success: boolean;
  data: IGame;
  message: string;
}

class GameService {
  async saveGame(
    gameData: SaveGameRequest
  ): Promise<{ success: boolean; data?: IGame; message: string }> {
    try {
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          message: "Token de autenticação não encontrado",
        };
      }

      const response = await fetch(`${BASE_URL}/games`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`,
        },
        body: JSON.stringify(gameData),
      });

      if (!response.ok) {
        if (response.status === 401) {
          return {
            success: false,
            message: "Token de autenticação inválido",
          };
        }
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const apiResponse: {
        success: boolean;
        data: IGame;
        message: string;
      } = await response.json();

      if (!apiResponse.success) {
        throw new Error(apiResponse.message || "API returned success: false");
      }

      return {
        success: true,
        data: apiResponse.data,
        message: "Game salvo com sucesso",
      };
    } catch (error) {
      console.error("Error saving game:", error);
      return {
        success: false,
        message: "Erro ao salvar game",
      };
    }
  }

  async getUserGames(): Promise<{ success: boolean; data: IGame[]; message: string }> {
    try {
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          data: [],
          message: "Token de autenticação não encontrado",
        };
      }

      const response = await fetch(`${BASE_URL}/games`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        if (response.status === 401) {
          return {
            success: false,
            data: [],
            message: "Token de autenticação inválido",
          };
        }
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const apiResponse: {
        success: boolean;
        data: IGame[];
        message: string;
      } = await response.json();

      if (!apiResponse.success) {
        throw new Error(apiResponse.message || "API returned success: false");
      }

      return {
        success: true,
        data: apiResponse.data,
        message: "Games do usuário carregados com sucesso",
      };
    } catch (error) {
      console.error("Error fetching user games:", error);
      return {
        success: false,
        data: [],
        message: "Erro ao carregar games do usuário",
      };
    }
  }

  async getGameById(
    id: string
  ): Promise<{ success: boolean; data: IGame; message: string }> {
    try {
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          data: {} as IGame,
          message: "Token de autenticação não encontrado",
        };
      }

      const response = await fetch(`${BASE_URL}/games/${id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        if (response.status === 401) {
          return {
            success: false,
            data: {} as IGame,
            message: "Token de autenticação inválido",
          };
        }
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const apiResponse: ApiResponse = await response.json();

      if (!apiResponse.success) {
        throw new Error(apiResponse.message || "API returned success: false");
      }

      return {
        success: true,
        data: apiResponse.data,
        message: "Game carregado com sucesso",
      };
    } catch (error) {
      console.error("Error fetching game:", error);
      return {
        success: false,
        data: {} as IGame,
        message: "Erro ao carregar game",
      };
    }
  }

  async getAllGames(): Promise<{
    success: boolean;
    data: IGame[];
    message: string;
  }> {
    try {
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          data: [],
          message: "Token de autenticação não encontrado",
        };
      }

      const response = await fetch(`${BASE_URL}/games`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        if (response.status === 401) {
          return {
            success: false,
            data: [],
            message: "Token de autenticação inválido",
          };
        }
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const apiResponse: { success: boolean; data: IGame[]; message: string } =
        await response.json();

      if (!apiResponse.success) {
        throw new Error(apiResponse.message || "API returned success: false");
      }

      return {
        success: true,
        data: apiResponse.data,
        message: "Games carregados com sucesso",
      };
    } catch (error) {
      console.error("Error fetching games:", error);
      return {
        success: false,
        data: [],
        message: "Erro ao carregar games",
      };
    }
  }
}

export const gameService = new GameService();
