import { useState, useEffect, useMemo } from "react";
import { Chess, Square } from "chess.js";
import { Chessboard } from "../../components/chessboard";
import { ChessboardControls } from "../../components/chessboard-controls/chessboard-controls";
import { AppTemplate } from "../../components/app-template";
import { StockfishEvaluation } from "../../components/stockfish-evaluation/stockfish-evaluation";
import "./opening.css";

interface OpeningMove {
  move?: string;
  san?: string;
  uci?: string;
  white: number;
  black: number;
  draws: number;
}

interface OpeningData {
  moves: OpeningMove[];
  white: number;
  black: number;
  draws: number;
  opening?: {
    eco?: string;
    name?: string;
  };
}

export function Opening() {
  const [game, setGame] = useState(() => new Chess());
  const [selectedPiece, setSelectedPiece] = useState<Square | null>(null);
  const [moves, setMoves] = useState<string[]>([]);
  const [currentMoveIndex, setCurrentMoveIndex] = useState(0);
  const [openingData, setOpeningData] = useState<OpeningData | null>(null);
  const [loading, setLoading] = useState(false);
  const [isEngineActive, setIsEngineActive] = useState(false);
  const [boardOrientation, setBoardOrientation] = useState<"white" | "black">("white");

  const possibleMoves = useMemo(() => {
    if (!selectedPiece) return [];
    try {
      return game
        .moves({ square: selectedPiece, verbose: true })
        .map((move) => move.to);
    } catch (error) {
      return [];
    }
  }, [game, selectedPiece]);

  // Fetch opening data from Lichess Masters database only
  const fetchOpeningData = async (fen: string) => {
    setLoading(true);
    try {
      // Use only Masters database (no fallbacks)
      const mastersResponse = await fetch(
        `https://explorer.lichess.ovh/masters?fen=${encodeURIComponent(
          fen
        )}&moves=10`,
        {
          method: "GET",
          headers: {
            Accept: "application/json",
          },
        }
      );

      if (mastersResponse.ok) {
        const mastersData = await mastersResponse.json();
        console.log("Masters API Response:", mastersData); // Debug
        setOpeningData(mastersData);
      } else {
        console.error(
          "Erro ao buscar dados de abertura:",
          mastersResponse.status
        );
        setOpeningData(null);
      }
    } catch (error) {
      console.error("Erro na requisição:", error);
      setOpeningData(null);
    } finally {
      setLoading(false);
    }
  };

  // Update opening data when game position changes
  useEffect(() => {
    fetchOpeningData(game.fen());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [game]);

  const handleSquareClick = (square: Square) => {
    if (selectedPiece) {
      if (selectedPiece === square) {
        setSelectedPiece(null);
        return;
      }

      try {
        const move = game.move({
          from: selectedPiece,
          to: square,
          promotion: "q",
        });

        if (move) {
          // Se estamos no meio da partida (não na última posição), cortar os lances futuros
          const newMoves =
            currentMoveIndex < moves.length
              ? [...moves.slice(0, currentMoveIndex), move.san]
              : [...moves, move.san];

          setMoves(newMoves);
          setCurrentMoveIndex(newMoves.length);
          setGame(new Chess(game.fen()));
          setSelectedPiece(null);
        } else {
          // Lance inválido, selecionar nova peça se houver uma peça válida no quadrado
          const piece = game.get(square);
          if (piece && piece.color === game.turn()) {
            setSelectedPiece(square);
          } else {
            setSelectedPiece(null);
          }
        }
      } catch (error) {
        // Lance impossível, tentar selecionar nova peça
        const piece = game.get(square);
        if (piece && piece.color === game.turn()) {
          setSelectedPiece(square);
        } else {
          setSelectedPiece(null);
        }
      }
    } else {
      const piece = game.get(square);
      if (piece && piece.color === game.turn()) {
        setSelectedPiece(square);
      }
    }
  };

  const handlePieceDrop = (
    sourceSquare: Square,
    targetSquare: Square
  ): boolean => {
    try {
      const move = game.move({
        from: sourceSquare,
        to: targetSquare,
        promotion: "q",
      });

      if (move) {
        // Se estamos no meio da partida (não na última posição), cortar os lances futuros
        const newMoves =
          currentMoveIndex < moves.length
            ? [...moves.slice(0, currentMoveIndex), move.san]
            : [...moves, move.san];

        setMoves(newMoves);
        setCurrentMoveIndex(newMoves.length);
        setGame(new Chess(game.fen()));
        setSelectedPiece(null);
        return true;
      }

      return false;
    } catch (error) {
      // Lance impossível, não fazer nada
      return false;
    }
  };

  const goToMove = (moveIndex: number) => {
    const newGame = new Chess();
    const movesToApply = moves.slice(0, moveIndex);

    try {
      movesToApply.forEach((move) => {
        newGame.move(move);
      });

      setGame(newGame);
      setCurrentMoveIndex(moveIndex);
      setSelectedPiece(null);
    } catch (error) {
      console.error("Erro ao aplicar movimentos:", error);
    }
  };

  const handlePrev = () => {
    if (currentMoveIndex > 0) {
      // Se estivermos na posição final (após todos os lances), remover o último lance
      if (currentMoveIndex === moves.length) {
        const newMoves = moves.slice(0, -1);
        setMoves(newMoves);
        setCurrentMoveIndex(newMoves.length);

        const newGame = new Chess();
        try {
          newMoves.forEach((move) => {
            newGame.move(move);
          });
          setGame(newGame);
        } catch (error) {
          console.error("Erro ao reconstruir posição:", error);
        }
      } else {
        // Caso contrário, apenas navegar para o lance anterior
        goToMove(currentMoveIndex - 1);
      }
      setSelectedPiece(null);
    }
  };

  const handleNext = () => {
    if (currentMoveIndex < moves.length) {
      goToMove(currentMoveIndex + 1);
    }
  };

  const handleReset = () => {
    setGame(new Chess());
    setMoves([]);
    setCurrentMoveIndex(0);
    setSelectedPiece(null);
  };

  const handleAnalyze = () => {
    setIsEngineActive(!isEngineActive);
  };

  const handleMainline = () => {
    if (openingData && openingData.moves && openingData.moves.length > 0) {
      // Get the most played move (first move in the array from Lichess, sorted by popularity)
      const mostPlayedMove = openingData.moves[0];
      const moveNotation =
        mostPlayedMove.move || mostPlayedMove.san || mostPlayedMove.uci;

      if (moveNotation) {
        makeMove(moveNotation);
      }
    }
  };

  const handleFlipBoard = () => {
    setBoardOrientation(boardOrientation === "white" ? "black" : "white");
  };


  const makeMove = (moveString: string) => {
    console.log("Making move:", moveString); // Debug
    try {
      const move = game.move(moveString);
      if (move) {
        // Se estamos no meio da partida (não na última posição), cortar os lances futuros
        const newMoves =
          currentMoveIndex < moves.length
            ? [...moves.slice(0, currentMoveIndex), move.san]
            : [...moves, move.san];

        setMoves(newMoves);
        setCurrentMoveIndex(newMoves.length);
        setGame(new Chess(game.fen()));
        setSelectedPiece(null);
      }
    } catch (error) {
      console.error("Movimento inválido:", error);
    }
  };

  const getTotalGames = (data: OpeningData) => {
    return data.white + data.black + data.draws;
  };

  const getMovePercentage = (move: OpeningMove, total: number) => {
    const moveTotal = move.white + move.black + move.draws;
    return ((moveTotal / total) * 100).toFixed(1);
  };

  const getWinPercentage = (move: OpeningMove) => {
    const total = move.white + move.black + move.draws;
    if (total === 0) return "0";
    return ((move.white / total) * 100).toFixed(1);
  };

  return (
    <AppTemplate title="Aberturas" withChessboard withoutHeaderShadow>
      <div className="App">
        <div className="chessboard-integrated-container">
          <div className="chessboard-container">
            <Chessboard
              game={game}
              customizedSquare={null}
              selectedPiece={selectedPiece}
              possibleMoves={possibleMoves}
              boardOrientation={boardOrientation}
              disabled={false}
              onDrop={handlePieceDrop}
              onSquareClick={handleSquareClick}
            />
          </div>
          <ChessboardControls
            current={currentMoveIndex}
            total={moves.length}
            onPrev={handlePrev}
            onNext={handleNext}
            onReset={handleReset}
            onAnalyze={handleAnalyze}
            onMainline={handleMainline}
            onFlipBoard={handleFlipBoard}
            isEngineActive={isEngineActive}
            mode="opening"
          />
        </div>

        {/* Engine Analysis */}
        {isEngineActive && (
          <div className="opening-engine-container">
            <div className="opening-engine-content">
              <h3>Análise da Engine</h3>
              <StockfishEvaluation
                fen={game.fen()}
                autoEvaluate={true}
                depth={15}
                showBestMove={true}
                compact={false}
              />
            </div>
          </div>
        )}

        {/* Popular Moves */}
        {loading ? (
          <div className="opening-moves-container">
            <div className="opening-moves-content">
              <h3>Movimentos Mais Jogados</h3>
              <div className="loading-stats">
                Carregando dados de mestres...
              </div>
            </div>
          </div>
        ) : openingData && openingData.moves && openingData.moves.length > 0 ? (
          <div className="opening-moves-container">
            <div className="opening-moves-content">
              <h3>Movimentos Mais Jogados</h3>
              <div className="moves-grid">
                {openingData.moves.slice(0, 6).map((move, index) => {
                  const moveNotation = move.move || move.san || move.uci;
                  console.log(
                    `Move ${index}:`,
                    move,
                    "notation:",
                    moveNotation
                  ); // Debug
                  return (
                    <div
                      key={`${moveNotation}-${index}`}
                      className="move-card"
                      onClick={() => {
                        if (moveNotation) {
                          makeMove(moveNotation);
                        }
                      }}
                    >
                      <div className="move-notation">
                        {moveNotation || "N/A"}
                      </div>
                      <div className="move-frequency">
                        {getMovePercentage(move, getTotalGames(openingData))}%
                      </div>
                      <div className="move-winrate">
                        ♔ {getWinPercentage(move)}%
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        ) : !loading ? (
          <div className="opening-moves-container">
            <div className="opening-moves-content">
              <h3>Movimentos Mais Jogados</h3>
              <div className="no-data">
                Nenhum dado de mestres disponível para esta posição.
                <br />
                <small>
                  Esta posição pode não ser comum em partidas de alto nível.
                </small>
              </div>
            </div>
          </div>
        ) : null}

        {/* Position Statistics */}
        {!loading && openingData && getTotalGames(openingData) > 0 && (
          <div className="opening-stats-container">
            <div className="opening-stats-content">
              <h3>Estatísticas da Posição</h3>
              <div className="position-stats">
                {openingData.opening?.eco && (
                  <div className="stat-item">
                    <span className="stat-label">Abertura ECO:</span>
                    <span className="stat-value">
                      {openingData.opening.eco}
                      {openingData.opening.name && ` - ${openingData.opening.name}`}
                    </span>
                  </div>
                )}
                <div className="stat-item">
                  <span className="stat-label">
                    Total de partidas de mestres:
                  </span>
                  <span className="stat-value">
                    {getTotalGames(openingData).toLocaleString()}
                  </span>
                </div>
                <div className="stat-item">
                  <span className="stat-label">Vitórias brancas:</span>
                  <span className="stat-value">
                    {openingData.white.toLocaleString()} (
                    {(
                      (openingData.white / getTotalGames(openingData)) *
                      100
                    ).toFixed(1)}
                    %)
                  </span>
                </div>
                <div className="stat-item">
                  <span className="stat-label">Vitórias pretas:</span>
                  <span className="stat-value">
                    {openingData.black.toLocaleString()} (
                    {(
                      (openingData.black / getTotalGames(openingData)) *
                      100
                    ).toFixed(1)}
                    %)
                  </span>
                </div>
                <div className="stat-item">
                  <span className="stat-label">Empates:</span>
                  <span className="stat-value">
                    {openingData.draws.toLocaleString()} (
                    {(
                      (openingData.draws / getTotalGames(openingData)) *
                      100
                    ).toFixed(1)}
                    %)
                  </span>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </AppTemplate>
  );
}
