import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import classNames from "classnames";
import { IGame, gameService } from "../../services/game-service";
import { usePuzzles } from "../../contexts/PuzzleContext";
import { useAuth } from "../../contexts/AuthContext";
import chessboardImage from "../../images/chessboard.jpeg";
import whiteKingIcon from "../../icons/white-king2.svg";
import blackKingIcon from "../../icons/black-king2.svg";
import { ReactComponent as BlackKingIcon } from "../../icons/black-king2.svg";
// import { ReactComponent as WhiteKingIcon } from "../../icons/white-king2.svg";
import { ReactComponent as PlusIcon } from "../../icons/plus.svg";
import { AppTemplate } from "../../components/app-template";
import Styles from "./games.module.scss";

export const Games: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const { puzzles, isLoading: puzzlesLoading } = usePuzzles();
  const [games, setGames] = useState<IGame[]>([]);
  const [myGames, setMyGames] = useState<IGame[]>([]);
  const [filteredGames, setFilteredGames] = useState<IGame[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [loading, setLoading] = useState(true);
  const [activeFilter, setActiveFilter] = useState<"all" | "mine">("all");

  useEffect(() => {
    const loadGames = async () => {
      if (!puzzlesLoading) {
        try {
          // Converter puzzles do contexto para IGame
          const gamesFromPuzzles: IGame[] = puzzles.map((puzzle) => ({
            id: puzzle.id,
            white: puzzle.white,
            black: puzzle.black,
            event: puzzle.event || "Puzzle",
            date: puzzle.date || new Date().toISOString(),
            fen: puzzle.fen,
            completedGame: puzzle.completedGame || [],
          }));

          setGames(gamesFromPuzzles);

          // Carregar jogos do usuário se estiver logado
          if (user?.id) {
            const userGamesResult = await gameService.getUserGames();
            if (userGamesResult.success) {
              setMyGames(userGamesResult.data);
            } else {
              console.error("Erro ao carregar jogos do usuário:", userGamesResult.message);
            }
          }

          setLoading(false);
        } catch (error) {
          console.error("Erro ao carregar games:", error);
          setLoading(false);
        }
      }
    };

    loadGames();
  }, [puzzles, puzzlesLoading, user]);

  // Filter games based on active filter and search term
  useEffect(() => {
    const currentGames = activeFilter === "mine" ? myGames : games;

    if (!searchTerm.trim()) {
      setFilteredGames(currentGames);
    } else {
      const filtered = currentGames.filter(
        (game) =>
          (game.white &&
            game.white.toLowerCase().includes(searchTerm.toLowerCase())) ||
          (game.black &&
            game.black.toLowerCase().includes(searchTerm.toLowerCase()))
      );
      setFilteredGames(filtered);
    }
  }, [searchTerm, games, myGames, activeFilter]);

  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
  };

  const handleGameClick = (gameId: string) => {
    // Se for "Minhas partidas", navegar para free-analysis com o jogo carregado
    if (activeFilter === "mine") {
      navigate(`/analise-livre?gameId=${gameId}`);
    } else {
      // Se for "Todas partidas", navegar para a tela normal de partida
      navigate(`/partida/${gameId}`);
    }
  };

  const handleAddGame = () => {
    navigate("/analise-livre");
  };

  if (loading || puzzlesLoading) {
    return (
      <AppTemplate title="Partidas">
        <div className={Styles.GamesPage}>
          <div className={Styles.FilterContainer}>
            <div className={Styles.FilterHeader}>
              <div className={Styles.TabsRow}>
                <button
                  className={classNames(Styles.TabButton, {
                    [Styles.TabButtonActive]: activeFilter === "all",
                  })}
                  onClick={() => setActiveFilter("all")}
                >
                  Todas partidas
                </button>
                <button
                  className={classNames(Styles.TabButton, {
                    [Styles.TabButtonActive]: activeFilter === "mine",
                  })}
                  onClick={() => setActiveFilter("mine")}
                >
                  Minhas partidas
                </button>
              </div>
              <button
                className={Styles.AddButton}
                onClick={handleAddGame}
                title="Adicionar nova partida"
              >
                <PlusIcon className={Styles.AddButtonIcon} />
              </button>
            </div>

            <div className={Styles.SkeletonCounter}></div>
            <div className={Styles.SkeletonSearchInput}></div>
          </div>
          <div className={Styles.GamesList}>
            {[1, 2, 3, 4, 5].map((index) => (
              <div
                key={index}
                className={classNames(Styles.GameItem, Styles.Skeleton)}
              >
                <div className={Styles.GameHeader}>
                  <div className={Styles.GameInfo}>
                    <div className={Styles.GamePlayers}>
                      <div
                        className={classNames(
                          Styles.SkeletonLine,
                          Styles.Short
                        )}
                      ></div>
                      <div className={Styles.PlayersNames}>
                        <div className={Styles.PlayerWhite}>
                          <div className={Styles.SkeletonKingIcon}></div>
                          <div
                            className={classNames(
                              Styles.SkeletonLine,
                              Styles.Medium
                            )}
                          ></div>
                        </div>
                        <div className={Styles.PlayerBlack}>
                          <div className={Styles.SkeletonKingIcon}></div>
                          <div
                            className={classNames(
                              Styles.SkeletonLine,
                              Styles.Medium
                            )}
                          ></div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className={Styles.SkeletonBoard}></div>
                </div>
                <div className={Styles.GameMeta}>
                  <div
                    className={classNames(Styles.SkeletonLine, Styles.Short)}
                  ></div>
                  <div
                    className={classNames(Styles.SkeletonLine, Styles.Tiny)}
                  ></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </AppTemplate>
    );
  }

  return (
    <AppTemplate title="Partidas">
      <div className={Styles.GamesPage}>
        <div className={Styles.FilterContainer}>
          <div className={Styles.FilterHeader}>
            <div className={Styles.TabsRow}>
              <button
                className={classNames(Styles.TabButton, {
                  [Styles.TabButtonActive]: activeFilter === "all",
                })}
                onClick={() => setActiveFilter("all")}
              >
                Todas partidas
              </button>
              <button
                className={classNames(Styles.TabButton, {
                  [Styles.TabButtonActive]: activeFilter === "mine",
                })}
                onClick={() => setActiveFilter("mine")}
              >
                Minhas partidas
              </button>
            </div>
            <button
              className={Styles.AddButton}
              onClick={handleAddGame}
              title="Adicionar nova partida"
            >
              <PlusIcon className={Styles.AddButtonIcon} />
            </button>
          </div>

          {(activeFilter === "all"
            ? games.length > 0
            : myGames.length > 0 || activeFilter === "mine") && (
            <div className={Styles.GamesCounter}>
              {searchTerm
                ? `${filteredGames.length} de ${
                    activeFilter === "mine" ? myGames.length : games.length
                  }`
                : `${
                    activeFilter === "mine" ? myGames.length : games.length
                  } partidas`}
            </div>
          )}

          <input
            type="text"
            placeholder="Buscar..."
            value={searchTerm}
            onChange={handleSearchChange}
            className={Styles.SearchInput}
          />
        </div>
        <div className={Styles.GamesList}>
          {filteredGames.length === 0 ? (
            <div className={Styles.EmptyState}>
              <div className={Styles.EmptyIcon}>
                {/* <WhiteKingIcon className={Styles.EmptyStateKingIcon} /> */}
                <BlackKingIcon className={Styles.EmptyStateKingIcon} />
              </div>
              <div className={Styles.EmptyMessage}>
                {activeFilter === "mine"
                  ? searchTerm
                    ? "Nenhuma das suas partidas encontrada"
                    : "Você ainda não tem partidas salvas"
                  : searchTerm
                  ? "Nenhuma partida encontrada"
                  : "Nenhuma partida disponível"}
              </div>
              {activeFilter === "mine" && !searchTerm ? (
                <div className={Styles.EmptySuggestion}>
                  Crie uma nova análise para salvar suas partidas
                </div>
              ) : (
                searchTerm && (
                  <div className={Styles.EmptySuggestion}>
                    Tente buscar por outro jogador
                  </div>
                )
              )}
            </div>
          ) : (
            filteredGames.map((game) => (
              <div
                key={game.id}
                className={Styles.GameItem}
                onClick={() => handleGameClick(game.id)}
              >
                <div className={Styles.GameHeader}>
                  <div className={Styles.GameInfo}>
                    <div className={Styles.GamePlayers}>
                      <div className={Styles.PlayersTitle}>Jogadores</div>
                      <div className={Styles.PlayersNames}>
                        {!game.white && !game.black ? (
                          <div>Jogadores desconhecidos</div>
                        ) : (
                          <>
                            <div className={Styles.PlayerWhite}>
                              <img
                                src={whiteKingIcon}
                                alt="Rei branco"
                                className={Styles.KingIcon}
                              />
                              <span>
                                {game.white || "Jogador desconhecido"}
                              </span>
                            </div>
                            <div className={Styles.PlayerBlack}>
                              <img
                                src={blackKingIcon}
                                alt="Rei preto"
                                className={Styles.KingIcon}
                              />
                              <span>
                                {game.black || "Jogador desconhecido"}
                              </span>
                            </div>
                          </>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className={Styles.GameBoard}>
                    <img
                      src={chessboardImage}
                      alt="Tabuleiro de xadrez"
                      className={Styles.BoardPreview}
                    />
                  </div>
                </div>

                <div className={Styles.GameMeta}>
                  <div className={Styles.GameEvent}>
                    {game.event || "Evento Desconhecido"}
                  </div>
                  <div className={Styles.GameDate}>
                    {game.date || "????.??.??"}
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </AppTemplate>
  );
};
