export const getUserById = async (
  userId: string
): Promise<GetUserByIdResponse | null> => {
  const token = getAuthToken();

  if (!token) {
    return null;
  }

  try {
    console.log("🚀 Buscando usuário por ID no backend:", userId);

    const response = await fetch(`${BASE_URL}/users/${userId}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
    });

    if (!response.ok) {
      console.error(
        "❌ Erro na resposta do backend:",
        response.status,
        response.statusText
      );

      if (response.status === 404) {
        return {
          success: false,
          user: {} as AdminUser,
          message: "Usuário não encontrado",
        };
      }

      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: GetUserByIdResponse = await response.json();

    console.log("✅ Usuário obtido com sucesso");
    console.log("👤 Nome:", result.user?.name);
    console.log("📧 Email:", result.user?.email);

    return result;
  } catch (error) {
    console.error("🚨 Erro ao buscar usuário:", error);
    return null;
  }
};

export const updateUserStatus = async (
  userId: string,
  status: "ACTIVE" | "INACTIVE"
): Promise<UpdateUserStatusResponse> => {
  const token = getAuthToken();

  if (!token) {
    return {
      success: false,
      message: "Token de autenticação não encontrado",
    };
  }

  try {
    console.log("🚀 Atualizando status do usuário no backend:");
    console.log("👤 ID do usuário:", userId);
    console.log("📊 Novo status:", status);

    const response = await fetch(`${BASE_URL}/users/${userId}/status`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ status }),
    });

    if (!response.ok) {
      console.error(
        "❌ Erro na resposta do backend:",
        response.status,
        response.statusText
      );

      if (response.status === 401) {
        return {
          success: false,
          message: "Token de autenticação inválido",
        };
      }

      if (response.status === 403) {
        return {
          success: false,
          message:
            "Acesso negado - apenas administradores podem alterar status de usuários",
        };
      }

      if (response.status === 404) {
        return {
          success: false,
          message: "Usuário não encontrado",
        };
      }

      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result: UpdateUserStatusResponse = await response.json();

    console.log("✅ Status do usuário atualizado com sucesso");

    return result;
  } catch (error) {
    console.error("🚨 Erro ao atualizar status do usuário:", error);

    // Fallback mock para desenvolvimento
    return {
      success: true,
      message: `Status do usuário alterado para ${status} com sucesso`,
    };
  }
};
