import React from "react";
import classNames from "classnames";
import { useAutoStockfishEvaluation } from "../../hooks/useStockfishEvaluation";
import Styles from "./stockfish-evaluation.module.scss";

interface StockfishEvaluationProps {
  fen: string | null;
  autoEvaluate?: boolean;
  depth?: number;
  showBestMove?: boolean;
  compact?: boolean;
  className?: string;
  barMode?: boolean;
  orientation?: "horizontal" | "vertical";
  boardSize?: number;
}

export const StockfishEvaluation: React.FC<StockfishEvaluationProps> = ({
  fen,
  autoEvaluate = true,
  depth = 15,
  showBestMove = false,
  compact = false,
  className = "",
  barMode = false,
  orientation = "horizontal",
  boardSize = 400,
}) => {
  const { evaluation, loading, error, formatEvaluation, getEvaluationColor } =
    useAutoStockfishEvaluation(fen, autoEvaluate, depth);

  if (!fen) {
    return null;
  }

  const evaluationText = formatEvaluation();
  const evaluationColor = getEvaluationColor();

  // Calcular a porcentagem de preenchimento baseada na avaliação
  const getBarFillPercentage = () => {
    if (!evaluation || error) return 50; // Centro se não há avaliação

    if (evaluation.mate !== undefined) {
      // Em posições de mate, vai para o extremo
      return evaluation.mate > 0 ? 100 : 0;
    }

    // Converter score para porcentagem (limitado entre -10 e +10 peões)
    const clampedScore = Math.max(-10, Math.min(10, evaluation.score));
    const percentage = ((clampedScore + 10) / 20) * 100;
    return percentage;
  };

  const formatScoreForBar = () => {
    if (!evaluation || error) return "0.0";

    if (evaluation.mate !== undefined) {
      return evaluation.mate > 0
        ? `#${evaluation.mate}`
        : `#${Math.abs(evaluation.mate)}`;
    }

    return Math.abs(evaluation.score).toFixed(1);
  };

  const getAdvantageColor = () => {
    if (!evaluation || error) return "equal";

    if (evaluation.mate !== undefined) {
      return evaluation.mate > 0 ? "white" : "black";
    }

    if (Math.abs(evaluation.score) < 0.15) return "equal";
    return evaluation.score > 0 ? "white" : "black";
  };

  if (barMode) {
    const fillPercentage = getBarFillPercentage();
    const scoreText = formatScoreForBar();
    const advantageColor = getAdvantageColor();
    const isHorizontal = orientation === "horizontal";

    const barStyle = {
      width: isHorizontal ? `${boardSize}px` : "24px",
      height: isHorizontal ? "24px" : `${boardSize}px`,
    };

    // Estilo para o preenchimento da barra
    const getFillStyle = () => {
      if (advantageColor === "white") {
        return isHorizontal
          ? { width: `${fillPercentage}%` }
          : { height: `${fillPercentage}%` };
      } else if (advantageColor === "black") {
        // Para preto, usar a porcentagem invertida (100 - fillPercentage)
        const blackPercentage = 100 - fillPercentage;
        return isHorizontal
          ? { width: `${blackPercentage}%` }
          : { height: `${blackPercentage}%` };
      }
      return {};
    };

    const fillStyle = getFillStyle();

    // Posições do score para ambos os lados quando igual
    const getScorePositions = () => {
      if (advantageColor !== "equal") {
        // Comportamento original para posições não iguais
        if (isHorizontal) {
          if (advantageColor === "white") {
            return [
              {
                side: "white",
                style: { left: "4px", top: "50%", transform: "translateY(-50%)" },
              },
            ];
          } else {
            return [
              {
                side: "black",
                style: { right: "4px", top: "50%", transform: "translateY(-50%)" },
              },
            ];
          }
        } else {
          if (advantageColor === "white") {
            return [
              {
                side: "white",
                style: {
                  bottom: "4px",
                  left: "50%",
                  transform: "translateX(-50%)",
                },
              },
            ];
          } else {
            return [
              {
                side: "black",
                style: {
                  top: "4px",
                  left: "50%",
                  transform: "translateX(-50%)",
                },
              },
            ];
          }
        }
      } else {
        // Quando igual, mostrar 0.0 em ambos os lados
        if (isHorizontal) {
          return [
            {
              side: "white",
              style: { left: "4px", top: "50%", transform: "translateY(-50%)" },
            },
            {
              side: "black",
              style: {
                right: "4px",
                top: "50%",
                transform: "translateY(-50%)",
              },
            },
          ];
        } else {
          return [
            {
              side: "white",
              style: {
                bottom: "4px",
                left: "50%",
                transform: "translateX(-50%)",
              },
            },
            {
              side: "black",
              style: { top: "4px", left: "50%", transform: "translateX(-50%)" },
            },
          ];
        }
      }
    };

    // Cor do score baseada no lado
    const getScoreColor = (side: string) => {
      return side === "white" ? "black" : "white";
    };

    const scorePositions = getScorePositions();

    return (
      <div
        className={classNames(Styles.StockfishEvaluation, Styles.BarMode, {
          [Styles.Horizontal]: orientation === "horizontal",
          [Styles.Vertical]: orientation === "vertical"
        }, className)}
      >
        <div className={Styles.EvaluationBar} style={barStyle}>
          <div className={Styles.BarBackground}>
            <div className={Styles.BarWhiteSide}></div>
            <div className={Styles.BarBlackSide}></div>
          </div>
          <div className={classNames(Styles.BarFill, {
            [Styles.White]: advantageColor === "white",
            [Styles.Black]: advantageColor === "black",
            [Styles.Equal]: advantageColor === "equal"
          })} style={fillStyle}></div>
          {!loading &&
            scorePositions.map((position, index) => (
              <div
                key={index}
                className={Styles.BarScoreContainer}
                style={position.style}
              >
                <span
                  className={classNames(Styles.BarScore, {
                    [Styles.White]: getScoreColor(position.side) === "white",
                    [Styles.Black]: getScoreColor(position.side) === "black",
                    [Styles.Mate]: evaluation?.mate !== undefined
                  })}
                >
                  {scoreText}
                </span>
              </div>
            ))}
          {loading && <div className={Styles.BarLoading}>⟳</div>}
        </div>
      </div>
    );
  }

  return (
    <div
      className={classNames(Styles.StockfishEvaluation, {
        [Styles.Compact]: compact
      }, className)}
    >
      <div className={Styles.EvaluationHeader}>
        <span className={Styles.EvaluationLabel}>
          {compact ? "Eval" : "Avaliação"}
        </span>
        {loading && <span className={Styles.EvaluationLoading}>⟳</span>}
      </div>

      <div className={Styles.EvaluationContent}>
        {error ? (
          <div className="evaluation-error">
            <span className={classNames(Styles.EvaluationScore, Styles.Error)}>--</span>
            {!compact && (
              <div className={Styles.EvaluationErrorText}>Erro na avaliação</div>
            )}
          </div>
        ) : (
          <div className="evaluation-result">
            <span className={classNames(Styles.EvaluationScore, {
              [Styles.White]: evaluationColor === "white",
              [Styles.Black]: evaluationColor === "black",
              [Styles.Equal]: evaluationColor === "equal"
            })}>
              {evaluationText}
            </span>

            {!compact && evaluation && (
              <div className={Styles.EvaluationDetails}>
                <div className={Styles.EvaluationDepth}>
                  Profundidade: {evaluation.depth}
                </div>

                {showBestMove && evaluation.bestMove && (
                  <div className={Styles.EvaluationBestMove}>
                    Melhor lance: {evaluation.bestMove}
                  </div>
                )}

                {evaluation.mate !== undefined && (
                  <div className={Styles.EvaluationMate}>
                    {evaluation.mate > 0
                      ? `Brancas têm mate em ${evaluation.mate}`
                      : `Pretas têm mate em ${Math.abs(evaluation.mate)}`}
                  </div>
                )}
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default StockfishEvaluation;
